// /home/coffee/my-keymap-viewer/src/hooks/useKanaChallengePractice.ts
import { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    PracticeHighlightResult,
    CharInfoSeion,
    allSeionCharInfos,
    CharInfoYouon, // 
    allYouonCharInfos, // 
    CharInfoDakuon, // 
    allDakuonCharInfos, // 
    CharInfoSokuonKomoji, // 
    allSokuonKomojiCharInfos, // 
    hid2Gyou,
    hid2Dan,
    functionKeyMaps, // 
    getHidKeyCodes, //  
    ChallengeResult, // 
} from './usePracticeCommons';

type ChallengeStatus = 'idle' | 'countdown' | 'running' | 'finished';
type KanaTarget = CharInfoSeion | CharInfoYouon | CharInfoDakuon | CharInfoSokuonKomoji; // | CharInfoHandakuon;
type ChallengeStage = 'gyouInput' | 'danInput' | 'youonInput' | 'dakuonInput' | 'tsuInput'; // 

const COUNTDOWN_SECONDS = 5;
const TRAINING_DURATION_MS = 60 * 1000; // 1
const CHALLENGE_DURATION_SECONDS = 60; // 

const useKanaChallengePractice = ({
    isActive,
    side,
    kb,
    layers,
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<ChallengeStatus>('idle');
    const [countdownValue, setCountdownValue] = useState<number>(COUNTDOWN_SECONDS);
    const [remainingTime, setRemainingTime] = useState<number>(TRAINING_DURATION_MS);
    const [currentTarget, setCurrentTarget] = useState<KanaTarget | null>(null);
    const [challengeStage, setChallengeStage] = useState<ChallengeStage>('gyouInput');
    const [headingChars, setHeadingChars] = useState<string[]>([]); //  state
    const [isFinished, setIsFinished] = useState(false); //  state

    const countdownTimerRef = useRef<number | null>(null);
    const trainingTimerRef = useRef<number | null>(null);
    const startTimeRef = useRef<number | null>(null);
    const correctCountRef = useRef(0); //  ()
    const missCountRef = useRef(0); // 
    const totalCharsTypedRef = useRef(0); // 
    const completedCharsCountRef = useRef(0); //  ()
    const currentConsecutiveCorrectRef = useRef(0); // 
    const maxConsecutiveCorrectRef = useRef(0); // 
    // totalAttemptedRef 
    // const totalAttemptedRef = useRef(0);
    const [challengeResults, setChallengeResults] = useState<ChallengeResult | null>(null);
    const prevIsActiveRef = useRef(isActive); // isActive 


    const currentFunctionKeyMap = useMemo(() => {
        return functionKeyMaps[kb]?.[side] ?? {};
    }, [kb, side]);

    const allKanaTargets = useMemo(() => {
        // 
        const tsuTargets = allSokuonKomojiCharInfos.filter(info => info.isTsu);
        // TODO: 
        return [...allSeionCharInfos, ...allYouonCharInfos, ...allDakuonCharInfos, ...tsuTargets];
    }, []);

    // 
    const selectNextTarget = useCallback(() => {
        if (allKanaTargets.length > 0) {
            const randomIndex = Math.floor(Math.random() * allKanaTargets.length);
            const nextTarget = allKanaTargets[randomIndex];
            //console.log(">>> Selecting new Kana Challenge target:", nextTarget);
            setCurrentTarget(nextTarget);
            if (nextTarget.type === 'sokuonKomoji' && nextTarget.isTsu) {
                setChallengeStage('tsuInput');
            } else {
                setChallengeStage('gyouInput');
            }
        } else {
            setCurrentTarget(null);
        }
    }, [allKanaTargets]);

    // 
    useEffect(() => {
        if (isActive && status === 'countdown') {
            if (countdownValue > 0) {
                countdownTimerRef.current = window.setTimeout(() => {
                    setCountdownValue(prev => prev - 1);
                }, 1000);
            } else {
                setStatus('running');
                setRemainingTime(TRAINING_DURATION_MS);
                startTimeRef.current = Date.now();
                // 
                correctCountRef.current = 0;
                missCountRef.current = 0;
                totalCharsTypedRef.current = 0;
                completedCharsCountRef.current = 0;
                currentConsecutiveCorrectRef.current = 0; // 
                maxConsecutiveCorrectRef.current = 0; // 
                // totalAttemptedRef.current = 0; // 
                selectNextTarget();
            }
        }
        return () => {
            if (countdownTimerRef.current !== null) {
                clearTimeout(countdownTimerRef.current);
            }
        };
    }, [isActive, status, countdownValue, selectNextTarget]);

    //  & 
    useEffect(() => {
        if (isActive && status === 'running') {
            trainingTimerRef.current = window.setInterval(() => {
                const elapsed = Date.now() - (startTimeRef.current ?? Date.now());
                const newRemaining = Math.max(0, TRAINING_DURATION_MS - elapsed);
                setRemainingTime(newRemaining);
                if (newRemaining === 0) {
                    setStatus('finished');
                    setIsFinished(true); // 
                    if (trainingTimerRef.current) clearInterval(trainingTimerRef.current); // 

                    const correct = correctCountRef.current; // 
                    const miss = missCountRef.current;
                    const totalTypedChars = totalCharsTypedRef.current; // 
                    const durationMinutes = (TRAINING_DURATION_MS / 1000) / 60;
                    const cpmValue = durationMinutes > 0 ? Math.round(completedCharsCountRef.current / durationMinutes) : 0; //  / ()
                    //  ( - ) /  
                    const accuracy = totalTypedChars > 0 ? Math.max(0, (totalTypedChars - miss) / totalTypedChars) : 0; // 0

                    const results: ChallengeResult = {
                        totalQuestions: correct, //  = 
                        totalCharsTyped: totalTypedChars, // 
                        correctCharsCount: completedCharsCountRef.current, //  ()
                        correctCount: correct, // 
                        missCount: miss, // 
                        accuracy: accuracy, // ( - ) / 
                        score: 0, // Score will be calculated in PracticeView
                        cpm: cpmValue,
                        maxConsecutiveCorrectChars: maxConsecutiveCorrectRef.current, // 
                        rankMessage: '', // Rank will be calculated in PracticeView
                    };
                    setChallengeResults(results); //  state 
                    setHeadingChars([]); //  App.tsx 
                }
            }, 100);
        } else if (status === 'finished' && trainingTimerRef.current) {
             clearInterval(trainingTimerRef.current); // 
        }
        return () => {
            if (trainingTimerRef.current !== null) {
                clearInterval(trainingTimerRef.current);
            }
        };
    // isFinished 
    }, [isActive, status, isFinished]);

    // 
    useEffect(() => {
        if (status === 'countdown') {
            setHeadingChars([`${countdownValue}`]);
        } else if (status === 'running' && currentTarget) {
            setHeadingChars([currentTarget.char]);
        } else if (status === 'finished') {
            setHeadingChars(['']);
        } else if (status === 'idle') {
            setHeadingChars([]);
        }
    }, [status, countdownValue, currentTarget]);


    // 
    const reset = useCallback(() => {
        setStatus('idle');
        setIsFinished(false); // 
        setCountdownValue(COUNTDOWN_SECONDS);
        setRemainingTime(TRAINING_DURATION_MS);
        setCurrentTarget(null);
        setChallengeStage('gyouInput');
        setHeadingChars([]); // 
        setChallengeResults(null); // 
        startTimeRef.current = null;
        correctCountRef.current = 0; // 
        missCountRef.current = 0;
        totalCharsTypedRef.current = 0; // 
        completedCharsCountRef.current = 0; // 
        currentConsecutiveCorrectRef.current = 0; // 
        maxConsecutiveCorrectRef.current = 0; // 
        // totalAttemptedRef.current = 0; // 
        if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        //console.log("Resetting Kana Challenge Practice");
    }, [setChallengeResults, setHeadingChars]);

    // 
    useEffect(() => {
        // isActive  false 
        if (!isActive && prevIsActiveRef.current) {
            // console.log(`[KanaChallenge useEffect] Resetting state because isActive became false.`);
            reset();
        } else if (isActive && status === 'idle') { // isActive  true  status  idle 
            setStatus('countdown');
            setCountdownValue(COUNTDOWN_SECONDS);
        }
        prevIsActiveRef.current = isActive; // 
    }, [isActive, status, reset]);


    // 
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        // isFinished 
        if (status !== 'running' || !currentTarget || isFinished) {
            return { isExpected: false, shouldGoToNext: false };
        }
        if (inputInfo.type !== 'release') {
            return { isExpected: false, shouldGoToNext: false };
        }

        totalCharsTypedRef.current += 1; //  ()

        const pressCode = inputInfo.pressCode;
        let isExpected = false;
        let nextStage: ChallengeStage | null = null;
        // let shouldIncrementAttempt = false; // totalAttemptedRef 
        let errorLayer: number | null = null; // Initialize errorLayer
        let isCorrectInput = false; // 

        //console.log(`[KanaChallenge] Input: 0x${pressCode.toString(16)} for target: ${currentTarget.char} (${currentTarget.type}), Stage: ${challengeStage}`);

        if (currentTarget.type === 'seion') {
            // ---  ---
            const expectedGyouKey = currentTarget.gyouKey;
            const expectedDanKey = currentTarget.danKey;
            const actualGyouKey = hid2Gyou(pressCode + 1, kb, side); // pressCode is 0-based, hid2Gyou expects 1-based
            const actualDanKey = hid2Dan(pressCode + 1, kb, side);   // pressCode is 0-based, hid2Dan expects 1-based

            if (challengeStage === 'gyouInput') {
                if (actualGyouKey === expectedGyouKey) {
                    isExpected = true;
                    nextStage = 'danInput';
                    errorLayer = null; // Correct input clears error
                } else {
                    nextStage = 'gyouInput';
                    errorLayer = 2; // Incorrect gyou input
                }
            } else if (challengeStage === 'danInput') {
                // shouldIncrementAttempt = true; // 
                if (actualDanKey === expectedDanKey) {
                    isExpected = true;
                    correctCountRef.current += 1;
                    isCorrectInput = true; // 
                    completedCharsCountRef.current += currentTarget.char.length; // 
                    selectNextTarget();
                    nextStage = null;
                    errorLayer = null; // Correct input clears error
                } else {
                    missCountRef.current += 1;
                    nextStage = 'gyouInput';
                    isCorrectInput = false; // 
                    errorLayer = 3; // Incorrect dan input
                }
            }
        } else if (currentTarget.type === 'youon') {
            // ---  ---
            const expectedGyouKey = currentTarget.gyouKey;
            const expectedDanKey = currentTarget.danKey;
            const actualGyouKey = hid2Gyou(pressCode + 1, kb, side); // 1-based
            const actualDanKey = hid2Dan(pressCode + 1, kb, side);   // 1-based
            const youonKeyCodeEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '');
            const expectedYouonKeyCode = youonKeyCodeEntry ? parseInt(youonKeyCodeEntry[0]) + 1 : -1;

            if (challengeStage === 'gyouInput') {
                if (actualGyouKey === expectedGyouKey) {
                    isExpected = true;
                    nextStage = 'youonInput'; // 
                    errorLayer = null; // Correct input clears error
                } else {
                    nextStage = 'gyouInput';
                    errorLayer = 2; // Incorrect gyou input
                }
            } else if (challengeStage === 'youonInput') {
                if ((pressCode + 1) === expectedYouonKeyCode) { // 1-based
                    isExpected = true;
                    nextStage = 'danInput';
                    errorLayer = null; // Correct input clears error
                } else {
                    // shouldIncrementAttempt = true; // 
                    missCountRef.current += 1;
                    isCorrectInput = false; // 
                    errorLayer = 2; // Incorrect youon input
                    nextStage = 'gyouInput';
                }
            } else if (challengeStage === 'danInput') {
                // shouldIncrementAttempt = true; // 
                if (actualDanKey === expectedDanKey) {
                    isExpected = true;
                    correctCountRef.current += 1;
                    isCorrectInput = true; // 
                    completedCharsCountRef.current += currentTarget.char.length; // 
                    selectNextTarget();
                    errorLayer = null; // Correct input clears error
                    nextStage = null;
                } else {
                    missCountRef.current += 1;
                    errorLayer = 3; // Incorrect dan input
                    isCorrectInput = false; // 
                    nextStage = 'gyouInput';
                }
            }
        } else if (currentTarget.type === 'dakuon') {
            // ---  ---
            const expectedGyouKey = currentTarget.gyouKey;
            const expectedDanKey = currentTarget.danKey;
            const actualGyouKey = hid2Gyou(pressCode + 1, kb, side); // 1-based
            const actualDanKey = hid2Dan(pressCode + 1, kb, side);   // 1-based
            const dakuonKeyCodeEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '  ');
            const expectedDakuonKeyCode = dakuonKeyCodeEntry ? parseInt(dakuonKeyCodeEntry[0]) + 1 : -1;

            if (challengeStage === 'gyouInput') {
                if (actualGyouKey === expectedGyouKey) {
                    isExpected = true;
                    nextStage = 'dakuonInput';
                    errorLayer = null; // Correct input clears error
                } else {
                    nextStage = 'gyouInput';
                    errorLayer = 2; // Incorrect gyou input
                }
            } else if (challengeStage === 'dakuonInput') {
                if ((pressCode + 1) === expectedDakuonKeyCode) { // 1-based
                    isExpected = true;
                    nextStage = 'danInput';
                    errorLayer = null; // Correct input clears error
                } else {
                    // shouldIncrementAttempt = true; // 
                    missCountRef.current += 1;
                    isCorrectInput = false; // 
                    errorLayer = 2; // Incorrect dakuon input
                    nextStage = 'gyouInput';
                }
            } else if (challengeStage === 'danInput') {
                // shouldIncrementAttempt = true; // 
                if (actualDanKey === expectedDanKey) {
                    isExpected = true;
                    correctCountRef.current += 1;
                    isCorrectInput = true; // 
                    completedCharsCountRef.current += currentTarget.char.length; // 
                    selectNextTarget();
                    errorLayer = null; // Correct input clears error
                    nextStage = null; // 
                } else {
                    missCountRef.current += 1;
                    errorLayer = 3; // Incorrect dan input
                    isCorrectInput = false; // 
                    nextStage = 'gyouInput'; // 
                }
            }
        } else if (currentTarget.type === 'sokuonKomoji' && currentTarget.isTsu) {
            // ---  ---
            const tsuKeyCodeEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '');
            const expectedTsuKeyCode = tsuKeyCodeEntry ? parseInt(tsuKeyCodeEntry[0]) + 1 : -1;

            if (challengeStage === 'tsuInput') {
                // shouldIncrementAttempt = true; // 
                if ((pressCode + 1) === expectedTsuKeyCode) { // 1-based
                    isExpected = true;
                    correctCountRef.current += 1;
                    isCorrectInput = true; // 
                    completedCharsCountRef.current += currentTarget.char.length; //  (1)
                    selectNextTarget();
                    errorLayer = null; // Correct input clears error
                    nextStage = null; //  selectNextTarget 
                } else {
                    missCountRef.current += 1;
                    errorLayer = 2; // Incorrect tsu input
                    isCorrectInput = false; // 
                    nextStage = 'tsuInput'; // 
                }
            }
        }

        let isSequenceCompletedThisTurn = false; //  ()

        // 
        if (currentTarget.type === 'seion' && challengeStage === 'danInput') {
            isSequenceCompletedThisTurn = true;
        } else if (currentTarget.type === 'youon' && challengeStage === 'danInput') {
            isSequenceCompletedThisTurn = true;
        } else if (currentTarget.type === 'dakuon' && challengeStage === 'danInput') {
            isSequenceCompletedThisTurn = true;
        } else if (currentTarget.type === 'sokuonKomoji' && currentTarget.isTsu && challengeStage === 'tsuInput') {
            isSequenceCompletedThisTurn = true;
        }

        // 
        // 1
        if (isSequenceCompletedThisTurn) {
            if (isCorrectInput) { // 
                currentConsecutiveCorrectRef.current += 1;
                maxConsecutiveCorrectRef.current = Math.max(maxConsecutiveCorrectRef.current, currentConsecutiveCorrectRef.current);
            } else { //  (isCorrectInput  false)
                // console.log(`[KanaChallenge] Resetting consecutive correct due to miss at sequence completion. missCount: ${missCountRef.current}, currentTarget: ${currentTarget?.char}`);
                currentConsecutiveCorrectRef.current = 0;
            }
        }
        // 

        // 
        // if (shouldIncrementAttempt) { // 
        //     totalAttemptedRef.current += 1;
        // }

        if (nextStage && nextStage !== challengeStage) {
            //console.log(`[KanaChallenge] Setting stage from ${challengeStage} to ${nextStage}`);
            setChallengeStage(nextStage);
        } else if (!isExpected && challengeStage !== 'gyouInput' && challengeStage !== 'tsuInput' && nextStage !== null) { // 
            // console.log(`[KanaChallenge] Incorrect input, resetting stage to gyouInput`);
             setChallengeStage('gyouInput');
        } else if (!isExpected && challengeStage === 'tsuInput') {
             //  tsuInput 
            // console.log(`[KanaChallenge] Incorrect input for tsu, staying in tsuInput stage`);
        }


        //console.log(`[KanaChallenge] End - isExpected: ${isExpected}, Final Stage: ${nextStage ?? challengeStage}, errorLayer: ${errorLayer}`);
        return { isExpected, shouldGoToNext: false, errorLayer }; // Return errorLayer
    // isFinished 
    }, [status, currentTarget, challengeStage, kb, side, selectNextTarget, currentFunctionKeyMap, isFinished, setChallengeStage]);

    // 
    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (status !== 'running' || !currentTarget || isFinished) {
            return noHighlight;
        }

        let expectedKeyName: string | null = null;
        let targetLayoutIndex: number | null = null;

        if (currentTarget.type === 'seion' || currentTarget.type === 'dakuon') {
            if (challengeStage === 'gyouInput') {
                expectedKeyName = currentTarget.gyouKey;
                targetLayoutIndex = 2;
            } else if (challengeStage === 'dakuonInput') {
                const dakuonKeyEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '  ');
                expectedKeyName = dakuonKeyEntry ? currentFunctionKeyMap[parseInt(dakuonKeyEntry[0])] : '  ';
                targetLayoutIndex = 2; // 2
            } else if (challengeStage === 'danInput') {
                expectedKeyName = currentTarget.danKey;
                targetLayoutIndex = 3;
            }
        } else if (currentTarget.type === 'youon') {
            // ---  ---
            const youonKeyEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '');
            const youonDisplayName = youonKeyEntry ? currentFunctionKeyMap[parseInt(youonKeyEntry[0])] : '';

            if (challengeStage === 'gyouInput') {
                expectedKeyName = currentTarget.gyouKey;
                targetLayoutIndex = 2;
            } else if (challengeStage === 'youonInput') {
                expectedKeyName = youonDisplayName;
                targetLayoutIndex = 2;
            } else if (challengeStage === 'danInput') {
                expectedKeyName = currentTarget.danKey;
                targetLayoutIndex = 3;
            }
        } else if (currentTarget.type === 'sokuonKomoji' && currentTarget.isTsu) {
            // ---  ---
            if (challengeStage === 'tsuInput') {
                const tsuKeyEntry = Object.entries(currentFunctionKeyMap).find(([_, name]) => name === '');
                expectedKeyName = tsuKeyEntry ? currentFunctionKeyMap[parseInt(tsuKeyEntry[0])] : '';
                targetLayoutIndex = 2; // 
            }
        }
        //  () 

        if (expectedKeyName !== null && layoutIndex === targetLayoutIndex && keyName === expectedKeyName) {
            return { className: 'bg-blue-100', overrideKey: null };
        }

        return noHighlight;
    // isFinished 
    }, [status, currentTarget, challengeStage, currentFunctionKeyMap, isFinished]);

    // 
    const isInvalidInputTarget = useCallback((
        targetPressCode: number, // 0-based code of the key that was actually pressed and found invalid
        currentLayoutIndex: number, // layoutIndex of the key being rendered
        currentKeyIndex: number, // 0-based keyIndex of the key being rendered
        pressedLayerIndex?: number // layerIndex where the invalid press occurred
    ): boolean => {
        // isFinished 
        if (status !== 'running' || !currentTarget || isFinished) return false;

        // This function is called by App.tsx.
        // targetPressCode is the 0-based index of the key that was pressed and found to be invalid.
        // pressedLayerIndex is the layer on which that invalid key was pressed.
        // currentLayoutIndex and currentKeyIndex refer to the key currently being rendered by KeyboardView.
        // We want to return true if the key being rendered IS the invalid key.

        if (pressedLayerIndex !== undefined) {
            return currentLayoutIndex === pressedLayerIndex && currentKeyIndex === targetPressCode;
        }
        // Fallback if pressedLayerIndex is not provided (should ideally not happen with current App.tsx)
        // This part of the logic might need refinement if pressedLayerIndex can be undefined
        // and we still need to identify the wrongly pressed key without knowing its layer.
        // For now, if layer isn't specified, we can't be sure, so return false.
        return false;
    }, [status, currentTarget, isFinished]); // Removed dependencies that are not directly used for this simplified logic

    const practiceResult = useMemo(() => {
        //console.log('[useKanaChallengePractice useMemo] Recalculating result object. currentTarget:', currentTarget, 'status:', status);
        return {
            currentTarget: currentTarget ?? undefined, // currentTarget  (null  undefined )
            getHighlight: () => ({ start: null, end: null }), // 
            headingChars,
            handleInput,
            getHighlightClassName,
            reset,
            isInvalidInputTarget,
            challengeResults, // 
            status, // status 
            countdownValue,
            challengeTimer: remainingTime,
            challengeTotalTime: TRAINING_DURATION_MS,
        };
    }, [currentTarget, headingChars, handleInput, getHighlightClassName, reset, isInvalidInputTarget, challengeResults, status, countdownValue, remainingTime]); // remainingTime 

    return practiceResult;
};

export default useKanaChallengePractice;
