export interface YouonKakuchoInputDef {
  char: string; // '', '' 
  gyouKey: string; // '' 
  dan: string; // '', ''  (youonKakuchoChars  dIdx )
}

export interface YouonKakuchoPracticeGroup {
  // gyouKey: string; // youonGyouList[gIdx] 
  inputs: (YouonKakuchoInputDef | null)[]; // dIdx (0-4)  null
}

//  (: )
// youonGyouList  youonKakuchoChars 
export const youonKakuchoPracticeData: YouonKakuchoPracticeGroup[] = youonGyouList.map(gyou => {
  const inputs: (YouonKakuchoInputDef | null)[] = [];
  const chars = (youonKakuchoChars[gyou] && youonKakuchoChars[gyou][0]) ? youonKakuchoChars[gyou][0].match(/.{1,2}/g) ?? [] : []; // "..."  ["", "", ...] 
  for (let i = 0; i < 5; i++) { // 5 ()
    if (i === 1 || i === 3) { // '' (dIdx=1)  '' (dIdx=3) 
      inputs.push({
        char: chars[i] ?? '', // 
        gyouKey: gyou,
        dan: i === 1 ? '' : '', // ''  '', ''  ''
      });
    } else {
      inputs.push(null); // 
    }
  }
  return { inputs };
});


