import React from 'react'; // useEffect 
import PracticeMenu from './PracticeMenu';
import PracticeHeading from './PracticeHeading';
import KeyboardLayout from './KeyboardLayout';
import {
    PracticeMode,
    PracticeHookResult,
    KeyboardSide,
    KeyboardModel,
    CharInfoGairaigo,
    PracticeStatus,
    HeadingCharDisplayInfo // Import the new type
} from '../hooks/usePracticeCommons';
import { layerNames, functionKeyMaps } from '../data/keymapData'; // functionKeyMaps 
import { calculateChallengeScoreAndRank } from '../utils/challengeUtils';
interface PracticeViewProps {
    practice: PracticeMode | '';
    handlePracticeSelect: (item: PracticeMode) => void;
    gIdx: number;
    dIdx: number;
    headingChars: string[] | HeadingCharDisplayInfo[]; // Update to accept the union type
    isChallengeFinished: boolean;
    activePractice: PracticeHookResult | null;
    isRandomMode: boolean;
    displayLayerIndices: number[];
    keyboardLayers: string[][];
    cols: number;
    fixedWidth: string;
    showKeyLabels: boolean;
    lastInvalidKeyCode: number | null;
    lastInvalidLayerIndex: number | null; // 
    currentFunctionKeyMap: Record<number, string>; // Changed from Record<string, string>
    training: boolean; // KeyboardLayout 
    // kb: KeyboardModel; // No longer needed if currentFunctionKeyMap is prepared by App.tsx
    // side: KeyboardSide; // No longer needed if currentFunctionKeyMap is prepared by App.tsx
    typedChar?: string | null; // App.tsx 
    isInvalidInputTarget: (targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number) => boolean; // App.tsx 
    isAlphaNumericMode?: boolean; // 
    // PracticeMenu  App.tsx 
    isKanaStepUpOpen: boolean;
    setIsKanaStepUpOpen: React.Dispatch<React.SetStateAction<boolean>>;
    isAlphaNumericSymbolStepUpOpen: boolean;
    setIsAlphaNumericSymbolStepUpOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

const isChallengeMode = (mode: PracticeMode | ''): boolean =>
    mode === '' ||
    mode === '' ||
    mode === '' ||
    mode === '' ||
    mode === '';

const PracticeView: React.FC<PracticeViewProps> = ({
    practice,
    handlePracticeSelect,
    gIdx,
    dIdx,
    headingChars,
    isChallengeFinished,
    activePractice,
    isRandomMode,
    displayLayerIndices,
    keyboardLayers,
    cols,
    fixedWidth,
    showKeyLabels,
    lastInvalidKeyCode,
    lastInvalidLayerIndex, // 
    currentFunctionKeyMap,
    training,
    // kb, // Removed
    // side, // Removed
    isInvalidInputTarget, // props 
    typedChar,
    isAlphaNumericMode = false,
    isKanaStepUpOpen,
    setIsKanaStepUpOpen,
    isAlphaNumericSymbolStepUpOpen,
    setIsAlphaNumericSymbolStepUpOpen,
}) => {
    console.log('[PracticeView] Component function executed (Top Level)');
    console.log(`[PracticeView RENDER PROPS] practice: "${practice}", headingChars (prop received):`, headingChars, `activePractice exists: ${!!activePractice}`);

    // 
    const renderHeading = () => {
        console.log(`[PracticeView renderHeading] Called. headingChars (from prop):`, headingChars);
        if (!headingChars || headingChars.length === 0) {
            let fallbackDisplayChar: string = '';
            if (activePractice?.targetChar) {
                if (typeof activePractice.targetChar === 'string') {
                    fallbackDisplayChar = activePractice.targetChar;
                } else if (typeof activePractice.targetChar === 'object' && activePractice.targetChar !== null && 'char' in activePractice.targetChar) {
                    // Handles objects like CharInfoGairaigo or YouonKakuchoHeadingCharInfo that have a 'char' property
                    fallbackDisplayChar = (activePractice.targetChar as { char: string }).char;
                }
            }
            console.log(`[PracticeView renderHeading] headingChars is empty, falling back to displayable char: ${fallbackDisplayChar}`);
            // headingChars targetChar 
            return <span>{fallbackDisplayChar ?? ''}</span>;
        }

        // headingChars  HeadingCharDisplayInfo[] 
        console.log(`[PracticeView renderHeading] Checking if headingChars[0] is object:`, typeof headingChars[0] === 'object', headingChars[0]);
        if (typeof headingChars[0] === 'object' && headingChars[0] !== null && 'char' in headingChars[0]) {
            console.log(`[PracticeView renderHeading] Rendering as HeadingCharDisplayInfo[]:`, headingChars);
            return (headingChars as HeadingCharDisplayInfo[]).map((item, index) => (
                <span key={index} className={item.isPracticeTarget ? "font-bold text-blue-500 dark:text-blue-300" : ""}>
                    {item.char}
                </span>
            )).reduce((prev, curr, index) => <>{prev}{index > 0 && <>&nbsp;</>}{curr}</>, <></>); // 
        }

        // headingChars  string[] 
        console.log(`[PracticeView renderHeading] Rendering as string[]:`, headingChars);
        return (headingChars as string[]).map((char, index) => (
             //  (props.targetChar) 
            <span key={index} className={char === activePractice?.targetChar ? "font-bold text-blue-500 dark:text-blue-300" : ""}>
                {char}
            </span>
        )).reduce((prev, curr, index) => <>{prev}{index > 0 && <>&nbsp;</>}{curr}</>, <></>); // 
    };

    return (
        <>
            {practice ? (
                <div className='grid grid-cols-3 gap-4 items-start'>
                    {/*  */}
                    <PracticeMenu
                        practice={practice}
                        isAlphaNumericMode={isAlphaNumericMode} // PracticeMenu 
                        handlePracticeSelect={handlePracticeSelect}
                        isKanaStepUpOpen={isKanaStepUpOpen}
                        setIsKanaStepUpOpen={setIsKanaStepUpOpen}
                        isAlphaNumericSymbolStepUpOpen={isAlphaNumericSymbolStepUpOpen}
                        setIsAlphaNumericSymbolStepUpOpen={setIsAlphaNumericSymbolStepUpOpen}
                    />

                    <div className="col-span-2 grid grid-cols-2 gap-4">
                        <div className={`${
                            'col-start-1 justify-self-center w-full' // 
                        }`}>
                            <>
                                {activePractice?.status === 'countdown' && activePractice?.countdownValue && activePractice.countdownValue > 0 && (
                                    <div className="flex items-center justify-center mb-4">
                                        <div className="text-5xl font-bold">
                                            {activePractice.countdownValue}
                                        </div>
                                    </div>
                                )}
                                {activePractice?.status !== 'countdown' && !isChallengeFinished && (
                                    <>
                                        {/*  */}
                                        {isChallengeMode(practice) && activePractice && activePractice.status === 'running' &&
                                            typeof activePractice.challengeTimer === 'number' &&
                                            typeof activePractice.challengeTotalTime === 'number' &&
                                            activePractice.challengeTotalTime > 0 && (
                                            <div className="w-full bg-gray-200 rounded-full h-2.5 mb-2 dark:bg-gray-700">
                                                {(() => {
                                                    const percentage = (activePractice.challengeTimer! / activePractice.challengeTotalTime!) * 100;
                                                    let barColorClass = 'bg-blue-600'; // 
                                                    if (percentage <= 10) {
                                                        barColorClass = 'bg-red-600'; // 10%
                                                    } else if (percentage <= 20) {
                                                        barColorClass = 'bg-green-500'; // 20% (Tailwind CSS)
                                                    }
                                                    return (
                                                        <div
                                                            className={`${barColorClass} h-2.5 rounded-full transition-all duration-300 ease-linear`}
                                                            style={{ width: `${Math.max(0, percentage)}%` }}
                                                        ></div>
                                                    );
                                                })()}
                                            </div>
                                        )}
                                        {/*  (PracticeHeading ) */}
                                        {console.log(`[PracticeView before PracticeHeading] Passing headingChars:`, headingChars, `to PracticeHeading`)}
                                        <PracticeHeading
                                            isRandomMode={isChallengeMode(practice) || isRandomMode} // 
                                            practice={practice}
                                            gIdx={gIdx}
                                            dIdx={dIdx}
                                            headingChars={headingChars} //  headingChars 
                                            isFinished={isChallengeFinished}
                                            activePractice={activePractice} // activePractice 
                                            typedEndIndex={activePractice?.typedEndIndex === null ? undefined : activePractice?.typedEndIndex}
                                            status={activePractice?.status}
                                            typedChar={typedChar} // typedChar  PracticeHeading 
                                            currentTargetCharForHighlight={
                                                practice === '' && activePractice?.currentTarget && typeof activePractice.currentTarget === 'object' && 'type' in activePractice.currentTarget && activePractice.currentTarget.type === 'gairaigo'
                                                    ? (activePractice.currentTarget as CharInfoGairaigo).char
                                                    : typeof activePractice?.currentTarget === 'string'
                                                        ? activePractice.currentTarget
                                                        : undefined
                                            }
                                        />
                                    </>
                                )}
                                {/* CapsLock */}
                                {practice === 'Caps Lock' && activePractice?.isCapsLockOn !== undefined && (
                                    <div className={`text-sm font-semibold p-1 rounded mt-2 text-center ${
                                        activePractice.isCapsLockOn
                                            ? 'bg-green-200 text-green-800 dark:bg-green-700 dark:text-green-200'
                                            : 'bg-yellow-200 text-yellow-800 dark:bg-yellow-700 dark:text-yellow-200 animate-pulse' // OFF
                                    }`}>
                                        {activePractice.isCapsLockOn ? 'CAPS LOCK: ON' : 'CAPS LOCKON'}
                                    </div>
                                )}
                            </>
                        </div>
                        {isChallengeFinished ? (
                            <div className="flex items-center p-4 border rounded bg-gray-50 col-start-1 justify-self-center" style={{ minHeight: '15rem' }}>
                                {activePractice?.challengeResults && (
                                    <pre className="text-lg text-left font-semibold">
                                        {`${practice} \n`}
                                        {`\t ${activePractice.challengeResults.totalQuestions}\n`}
                                        {/*  */}
                                        {(practice === '' || practice === '' || practice === '') &&
                                            `\t ${activePractice.challengeResults.correctCharsCount}\n`
                                        }
                                        {`\t\t ${(activePractice.challengeResults.accuracy * 100).toFixed(1)}%\n`}
                                        {/* cpm */}
                                        {typeof activePractice.challengeResults.cpm === 'number' &&
                                            `\t ${activePractice.challengeResults.cpm} CPM\n`
                                        }
                                        {/* maxConsecutiveCorrectChars */}
                                        {typeof activePractice.challengeResults.maxConsecutiveCorrectChars === 'number' &&
                                            `${activePractice.challengeResults.maxConsecutiveCorrectChars}\n`
                                        }
                                        {/*  calculateChallengeScoreAndRank  */}
                                        {(() => {
                                            const calculatedResults = calculateChallengeScoreAndRank(activePractice.challengeResults);
                                            return `\t ${calculatedResults.score}\n\n${calculatedResults.rankMessage}`;
                                        })()}
                                    </pre>
                                )}
                            </div>
                        ) : (
                            // OFF
                            (!isChallengeMode(practice) || showKeyLabels) && (
                                <>
                                    {displayLayerIndices.length === 1 ? (
                                        keyboardLayers[displayLayerIndices[0]] && (
                                            <KeyboardLayout
                                                key={displayLayerIndices[0]}
                                                layerData={keyboardLayers[displayLayerIndices[0]]}
                                                layoutIndex={displayLayerIndices[0]}
                                                className="col-start-1 justify-self-center w-full"
                                                layoutTitle={layerNames[displayLayerIndices[0]] ?? ` ${displayLayerIndices[0]}`}
                                                cols={cols} fixedWidth={fixedWidth} showKeyLabels={showKeyLabels} lastInvalidKeyCode={lastInvalidKeyCode} lastInvalidLayerIndex={lastInvalidLayerIndex} activePractice={activePractice} practice={practice} currentFunctionKeyMap={currentFunctionKeyMap} training={training}
                                                isInvalidInputTarget={isInvalidInputTarget}
                                            />
                                        )
                                    ) : displayLayerIndices.length === 2 ? (
                                        displayLayerIndices.map((layerIndex, i) => { // 'i' 
                                            // (col-start-1)2(col-start-2)
                                            const gridColClass = i === 0 ? 'col-start-1 justify-self-center' : 'col-start-2 justify-self-center';
                                            return keyboardLayers[layerIndex] ? (
                                                <KeyboardLayout
                                                    key={layerIndex}
                                                    layerData={keyboardLayers[layerIndex]}
                                                    layoutIndex={layerIndex}
                                                    className={gridColClass}
                                                    layoutTitle={layerNames[layerIndex] ?? ` ${layerIndex}`}
                                                    cols={cols} fixedWidth={fixedWidth} showKeyLabels={showKeyLabels} lastInvalidKeyCode={lastInvalidKeyCode} lastInvalidLayerIndex={lastInvalidLayerIndex} activePractice={activePractice} practice={practice} currentFunctionKeyMap={currentFunctionKeyMap} training={training}
                                                    isInvalidInputTarget={isInvalidInputTarget}
                                                />
                                            ) : (
                                                <div key={layerIndex} className={gridColClass}> {layerIndex} </div>
                                            );
                                        })
                                    ) : (
                                        displayLayerIndices.length > 0 && <div className="col-span-2"></div> // 12
                                    )
                                    }
                                </>
                            )
                        )}
                    </div>
                </div>
            ) : (
                <div className='grid grid-cols-3 gap-4 items-start'>
                    <PracticeMenu
                        practice={practice}
                        isAlphaNumericMode={isAlphaNumericMode} // isAlphaNumericMode 
                        handlePracticeSelect={handlePracticeSelect}
                        isKanaStepUpOpen={isKanaStepUpOpen}
                        setIsKanaStepUpOpen={setIsKanaStepUpOpen}
                        isAlphaNumericSymbolStepUpOpen={isAlphaNumericSymbolStepUpOpen}
                        setIsAlphaNumericSymbolStepUpOpen={setIsAlphaNumericSymbolStepUpOpen}
                    />
                    <div className="col-span-2">
                        {/*  */}
                    </div>
                </div>
            )}
        </>
    );
};

export default PracticeView;
