// /home/coffee/my-keymap-viewer/src/hooks/usePracticeManagement.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    // These types should come from usePracticeCommons
    PracticeMode,
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo, // Import PracticeInputInfo for the default handleInput
    KeyboardSide,
    KeyboardModel,
    HeadingCharDisplayInfo, // Import the new type
} from './usePracticeCommons'; // Corrected import path
import {
    gyouList, // gyouList 
    youonGyouList, // youonGyouList 
    dakuonGyouList, // dakuonGyouList 
    handakuonGyouList, // handakuonGyouList 
    sokuonKomojiData, // sokuonKomojiData 
    kigoPractice1Data, // kigoPractice1Data 
    kigoPractice2Data, // kigoPractice2Data 
    kigoPractice3Data, // kigoPractice3Data 
    youdakuonPracticeData, // youdakuonPracticeData 
    youhandakuonPracticeData, // youhandakuonPracticeData 
    youonKakuchoPracticeData, // youonKakuchoPracticeData 
    gairaigoPracticeData, // gairaigoPracticeData 
    // These data arrays are used in nextStage logic, ensure they are imported
    gyouChars,
    youonGyouChars,
    dakuonGyouChars,
    handakuonGyouChars,
    alphaSymbolPractice1Data, // Import alphaSymbolPractice1Data
    alphabetPracticeGroups, // alphabetPracticeGroups 
    alphaSymbolPractice2Data, // Import alphaSymbolPractice2Data for nextStage logic
    // numberPracticeGroups, // numberPracticeGroups  App.tsx 
} from '../data/keymapData'; // 

// 
import useSeionPractice from './useSeionPractice';
import useYouonPractice from './useYouonPractice';
import useDakuonPractice from './useDakuonPractice';
import useHandakuonPractice from './useHandakuonPractice';
import useSokuonKomojiPractice from './useSokuonKomojiPractice';
import useKigoPractice1 from './useKigoPractice1';
import useKigoPractice2 from './useKigoPractice2';
import useKigoPractice3 from './useKigoPractice3';
import useYoudakuonPractice from './useYoudakuonPractice';
import { useYouhandakuonPractice } from './useYouhandakuonPractice'; // Changed to named import
import useYouonKakuchoPractice from './useYouonKakuchoPractice';
import useGairaigoPractice from './useGairaigoPractice';
import useKanaChallengePractice from './useKanaChallengePractice';
import useKigoChallengePractice from './useKigoChallengePractice';
import useTanbunChallengePractice from './useTanbunChallengePractice';
import useAlphabetPractice from './useAlphabetPractice';
import useNumberPractice from './useNumberPractice';
import useCapsLockPractice from './useCapsLockPractice';
import useCorrectionPractice from './useCorrectionPractice';
import useAlphabetChallengePractice from './useAlphabetChallengePractice';
import useAlphaNumTanbunChallengePractice from './useAlphaNumTanbunChallengePractice';
import useAlphaSymbolPractice1 from './useAlphaSymbolPractice1';
import useAlphaSymbolPractice2 from './useAlphaSymbolPractice2'; // 
import useKigoPractice3SymbolMenu from './useKigoPractice3SymbolMenu'; // 


interface UsePracticeManagementProps {
    side: KeyboardSide;
    layers: string[][];
    kb: KeyboardModel;
    // showKeyLabels: boolean; // Removed: To be managed internally based on challenge mode
    isAlphaNumericMode: boolean; // App.tsx 
}

// usePracticeManagement 
// (PracticeHookResult )
interface PracticeManagementResult extends PracticeHookResult {
    practice: PracticeMode | ''; // practice  PracticeManagementResult 
    gIdx: number;
    dIdx: number;
    isRandomMode: boolean;
    activePractice: PracticeHookResult | null;
    headingChars: string[] | HeadingCharDisplayInfo[]; // Update the type here
    isChallengeFinished: boolean;
    handlePracticeSelect: (item: PracticeMode) => void;
    toggleRandomMode: () => void;
    setPractice: React.Dispatch<React.SetStateAction<PracticeMode | ''>>;
    setGIdx: React.Dispatch<React.SetStateAction<number>>;
    setDIdx: React.Dispatch<React.SetStateAction<number>>;
    setIsRandomMode: React.Dispatch<React.SetStateAction<boolean>>;
    nextStage: (currentPractice: PracticeMode) => void;
    isRandomModeAvailable: boolean; // isRandomModeAvailable  PracticeManagementResult 
    showChallengeKeys: boolean; // showChallengeKeys  PracticeManagementResult 
    toggleShowChallengeKeys: () => void; // toggleShowChallengeKeys  PracticeManagementResult 
}


const usePracticeManagement = (props: UsePracticeManagementProps): PracticeManagementResult => {
    const { side, layers, kb, isAlphaNumericMode } = props;

    const [practice, setPractice] = useState<PracticeMode | ''>('');
    const [gIdx, setGIdx] = useState(0);
    const [dIdx, setDIdx] = useState(0);
    const [isRandomMode, setIsRandomMode] = useState(false);
    const [showChallengeKeys, setShowChallengeKeys] = useState<boolean>(false); // OFF
    const [isChallengeFinished, setIsChallengeFinished] = useState(false);

    const toggleShowChallengeKeys = useCallback(() => {
        setShowChallengeKeys(prev => !prev);
    }, []);

    const nextStage = useCallback((currentPractice: PracticeMode) => {
        console.log(`[PracticeManagement nextStage CALLED] Practice: ${currentPractice}, Current gIdx: ${gIdx}, dIdx: ${dIdx}, isRandomMode: ${isRandomMode}`);
        try { throw new Error('Actual call stack'); } catch (e: any) { console.log(e.stack); }

        // Challenge modes manage their own progression or end state.
        const challengeModes: PracticeMode[] = [
            '', '', '',
            '', ''
        ];
        if (challengeModes.includes(currentPractice)) {
            return;
        }

        // Practices that self-manage their random progression.
        // For these, usePracticeManagement should not alter gIdx/dIdx if nextStage is called in random mode.
        // Ideally, these hooks return `shouldGoToNext: false` in random mode, so App.tsx doesn't call nextStage.
        const selfManagingRandomPractices: PracticeMode[] = [
            '', '', '', '', '',
            '', '', '', '',
            '', '', '', ''
            // '' 
            // '' 
        ];

        if (isRandomMode && selfManagingRandomPractices.includes(currentPractice)) {
            console.warn(`[PracticeManagement nextStage] Called for self-managing random practice "${currentPractice}". Doing nothing regarding gIdx/dIdx advancement by usePracticeManagement.`);
            return;
        }
        
        if (isRandomMode && (currentPractice === '' || currentPractice === '')) {
            console.warn(`[PracticeManagement nextStage] Called for '${currentPractice}' in random mode. Hook handles its own progression.`);
            return;
        }

        // ''  gIdx/dIdx  App.tsx  setDIdx 
        // useNumberPractice  isRandomMode  true 
        // isRandomMode  false props.gIdx (App.tsx  dIdx ) 
        // App.tsx  handleHidInputReport  shouldGoToNext: true  setDIdx 
        //  usePracticeManagement  dIdx state 
        // useNumberPractice  gIdx prop 
        //  gIdx/dIdx 
        // if (currentPractice === '') {
        //     if (!isRandomMode) {
        //         setDIdx(prevDIdx => { // gIdx  dIdx  (App.tsx )
        //             const nextDIdx = prevDIdx + 1;
        //             return nextDIdx >= "1234567890".length ? 0 : nextDIdx;
        //         });
        //     }
        //     // useNumberPractice 
        //     return;
        // }


        let currentGroupData: any[] | undefined;
        let dIdxLimit: number | undefined;

        switch (currentPractice) {
            case '':
                currentGroupData = gyouList;
                dIdxLimit = gyouChars[gyouList[gIdx]]?.length;
                break;
            case '':
                currentGroupData = youonGyouList;
                dIdxLimit = youonGyouChars[youonGyouList[gIdx]]?.length;
                break;
            case '':
                currentGroupData = dakuonGyouList;
                dIdxLimit = dakuonGyouChars[dakuonGyouList[gIdx]]?.length;
                break;
            case '':
                currentGroupData = handakuonGyouList;
                dIdxLimit = handakuonGyouChars[handakuonGyouList[0]]?.length;
                break;
            case '':
                currentGroupData = sokuonKomojiData;
                dIdxLimit = sokuonKomojiData[gIdx]?.inputs.length;
                break;
            case '':
                currentGroupData = kigoPractice1Data;
                dIdxLimit = kigoPractice1Data[gIdx]?.chars.length;
                console.log(`[PracticeManagement nextStage KIGO1] gIdx: ${gIdx}, dIdx: ${dIdx}, dIdxLimit: ${dIdxLimit}, currentGroupData exists: ${!!currentGroupData}`);
                break;
            case '':
                currentGroupData = kigoPractice2Data;
                dIdxLimit = kigoPractice2Data[gIdx]?.chars.length;
                break;
            case '':
            case '_': // 
                currentGroupData = kigoPractice3Data;
                dIdxLimit = kigoPractice3Data[gIdx]?.chars.length;
                console.log(`[PracticeManagement nextStage KIGO3] gIdx: ${gIdx}, dIdx: ${dIdx}, dIdxLimit: ${dIdxLimit}, currentGroupData exists: ${!!currentGroupData}`);
                break;
            case '':
                currentGroupData = youdakuonPracticeData;
                dIdxLimit = youdakuonPracticeData[gIdx]?.chars.length;
                break;
            case '':
                currentGroupData = youhandakuonPracticeData;
                dIdxLimit = youhandakuonPracticeData[0]?.chars.length;
                break;
            case '':
                if (dIdx === 1) {
                    setDIdx(3);
                    return;
                }
                currentGroupData = youonKakuchoPracticeData;
                dIdxLimit = 4; 
                break;
            case '':
                currentGroupData = gairaigoPracticeData;
                dIdxLimit = gairaigoPracticeData[gIdx]?.targets.length;
                break;
            case '':
            case 'Caps Lock':
            case '':
                currentGroupData = alphabetPracticeGroups;
                dIdxLimit = alphabetPracticeGroups[gIdx]?.length;
                break;
            case '': // 
                currentGroupData = alphaSymbolPractice1Data;
                dIdxLimit = alphaSymbolPractice1Data[gIdx]?.chars.length;
                break;
            case '': // 
                currentGroupData = alphaSymbolPractice2Data;
                dIdxLimit = alphaSymbolPractice2Data[gIdx]?.chars.length;
                break;
            default:
                return;
        }

        if (dIdxLimit === undefined || currentGroupData === undefined) return;

        if (dIdx < dIdxLimit - 1) {
            console.log(`[PracticeManagement nextStage IF_BLOCK] Practice: ${currentPractice}, Condition (dIdx < dIdxLimit - 1) is TRUE. dIdx: ${dIdx}, dIdxLimit: ${dIdxLimit}. Incrementing dIdx.`);
            setDIdx(prevDIdx => prevDIdx + 1);
        } else {
            console.log(`[PracticeManagement nextStage ELSE_BLOCK] Practice: ${currentPractice}, Condition (dIdx < dIdxLimit - 1) is FALSE. dIdx: ${dIdx}, dIdxLimit: ${dIdxLimit}. Incrementing gIdx.`);
            if (currentPractice === '') { // Only for normal mode
                setDIdx(1);
            } else {
                setDIdx(0);
            }
            setGIdx(prevGIdx => {
                const nextGIdx = prevGIdx + 1;
                if (nextGIdx >= currentGroupData!.length) {
                    return 0;
                }
                return nextGIdx;
            });
        }
    }, [gIdx, dIdx, setGIdx, setDIdx, isRandomMode]);

    // Common props for all practice hooks
    const basePracticeHookProps = useMemo((): Omit<PracticeHookProps, 'isActive'> => { // isActive will be added per hook
        const isCurrentPracticeChallenge = [
            '', '', '',
            '', ''
        ].includes(practice);

        return {
            showKeyLabels: isCurrentPracticeChallenge ? showChallengeKeys : true,
            gIdx,
            dIdx, // This dIdx from usePracticeManagement state is passed to most hooks
            side,
            layers,
            kb,
            isRandomMode,
            onAdvance: () => nextStage(practice),
            isAlphaNumericMode,
        };
    }, [gIdx, dIdx, side, layers, kb, isRandomMode, practice, showChallengeKeys, nextStage, isAlphaNumericMode]);

    // Special props for useNumberPractice: its gIdx prop should be usePracticeManagement's dIdx state
    const numberPracticeHookProps = useMemo((): Omit<PracticeHookProps, 'isActive'> => {
        const isCurrentPracticeChallenge = [
            '', '', '',
            '', ''
        ].includes(practice);

        return {
            showKeyLabels: isCurrentPracticeChallenge ? showChallengeKeys : true,
            gIdx: dIdx, // Pass usePracticeManagement's dIdx as gIdx to useNumberPractice
            dIdx: 0,    // useNumberPractice doesn't use its own dIdx prop, so pass a dummy value
            side,
            layers,
            kb,
            isRandomMode,
            onAdvance: () => nextStage(practice),
            isAlphaNumericMode,
        };
    }, [dIdx, side, layers, kb, isRandomMode, practice, showChallengeKeys, nextStage, isAlphaNumericMode]);


    // 
    const seionPractice = useSeionPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const youonPractice = useYouonPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const dakuonPractice = useDakuonPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const handakuonPractice = useHandakuonPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const sokuonKomojiPractice = useSokuonKomojiPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const kigoPractice1 = useKigoPractice1({ ...basePracticeHookProps, isActive: practice === '' });
    const kigoPractice2 = useKigoPractice2({ ...basePracticeHookProps, isActive: practice === '' });
    const kigoPractice3 = useKigoPractice3({ ...basePracticeHookProps, isActive: practice === '' });
    const youdakuonPractice = useYoudakuonPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const youhandakuonPractice = useYouhandakuonPractice({ // Changed to accept a single props object
        ...basePracticeHookProps,
        practice: practice as PracticeMode, // Ensure practice is passed if needed by the hook internally, and cast
        isActive: practice === ''
    });
    const youonKakuchoPractice = useYouonKakuchoPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const gairaigoPractice = useGairaigoPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const kanaChallengePractice = useKanaChallengePractice({ ...basePracticeHookProps, isActive: practice === '' });
    const kigoChallengePractice = useKigoChallengePractice({ ...basePracticeHookProps, isActive: practice === '' });
    const tanbunChallengePractice = useTanbunChallengePractice({ ...basePracticeHookProps, isActive: practice === '' });
    const alphabetPractice = useAlphabetPractice({ ...basePracticeHookProps, isActive: practice === '' }); // Uses basePracticeHookProps.gIdx and basePracticeHookProps.dIdx
    const numberPractice = useNumberPractice({ ...numberPracticeHookProps, isActive: practice === '' }); // Uses numberPracticeHookProps
    const capsLockPractice = useCapsLockPractice({ ...basePracticeHookProps, isActive: practice === 'Caps Lock' });
    const correctionPractice = useCorrectionPractice({ ...basePracticeHookProps, isActive: practice === '' });
    const alphabetChallengePractice = useAlphabetChallengePractice({
        ...basePracticeHookProps,
        isActive: practice === '',
        practiceName: '', // practiceName 
    });
    const alphaNumTanbunChallengePractice = useAlphaNumTanbunChallengePractice({
        ...basePracticeHookProps,
        isActive: practice === '',
        practiceName: '', // practiceName 
    });
    const alphaSymbolPractice1 = useAlphaSymbolPractice1({
        ...basePracticeHookProps,
        isActive: practice === '',
        // isRandomMode is already in basePracticeHookProps
    });
    const alphaSymbolPractice2 = useAlphaSymbolPractice2({
        ...basePracticeHookProps,
        isActive: practice === '',
        // isRandomMode is already in basePracticeHookProps
    });
    const kigoPractice3SymbolMenu = useKigoPractice3SymbolMenu({ // kigoPractice3SymbolMenu 
        ...basePracticeHookProps,
        isActive: practice === '' || practice === '_',
        practice: practice, // Pass the current practice mode
    });


    const activePractice: PracticeHookResult | null = useMemo(() => {
        switch (practice) {
            // 
            case '': return seionPractice;
            case '': return youonPractice;
            case '': return dakuonPractice;
            case '': return handakuonPractice;
            case '': return sokuonKomojiPractice;
            case '': return kigoPractice1;
            case '': return kigoPractice2; // 
            case '':
                return {
                    headingChars: youdakuonPractice.headingChars,
                    handleInput: youdakuonPractice.handleInput,
                    getHighlightClassName: youdakuonPractice.getHighlightClassName,
                    reset: youdakuonPractice.reset,
                    isInvalidInputTarget: youdakuonPractice.isInvalidInputTarget,
                    currentTarget: youdakuonPractice.currentTarget,
                    errorHighlightLayer: youdakuonPractice.errorHighlightLayer,
                    clearErrorState: youdakuonPractice.clearErrorState,
                    targetLayerIndices: youdakuonPractice.targetLayerIndices,
                    stage: youdakuonPractice.stage,
                    status: youdakuonPractice.status,
                    targetChar: youdakuonPractice.targetChar,
                    targetLayerIndex: youdakuonPractice.targetLayerIndex,
                };
            case '':
                return {
                    status: youhandakuonPractice.status,
                    targetChar: youhandakuonPractice.targetChar,
                    currentTarget: youhandakuonPractice.currentTarget,
                    headingChars: youhandakuonPractice.headingChars,
                    targetLayerIndex: youhandakuonPractice.targetLayerIndex,
                    targetLayerIndices: youhandakuonPractice.targetLayerIndices,
                    errorHighlightLayer: youhandakuonPractice.errorHighlightLayer,
                    handleInput: youhandakuonPractice.handleInput,
                    getHighlightClassName: youhandakuonPractice.getHighlightClassName,
                    reset: youhandakuonPractice.reset,
                    isInvalidInputTarget: youhandakuonPractice.isInvalidInputTarget,
                    clearErrorState: youhandakuonPractice.clearErrorState,
                    stage: youhandakuonPractice.stage,
                    countdownValue: youhandakuonPractice.countdownValue,
                    challengeResults: youhandakuonPractice.challengeResults,
                    challengeTimer: youhandakuonPractice.challengeTimer,
                    challengeTotalTime: youhandakuonPractice.challengeTotalTime,
                    capitalizedCharIndices: youhandakuonPractice.capitalizedCharIndices,
                    typedEndIndex: youhandakuonPractice.typedEndIndex,
                };
            case '': return youonKakuchoPractice;
            case '': return gairaigoPractice;
            case '': return kanaChallengePractice;
            case '': return kigoChallengePractice;
            case '': return tanbunChallengePractice;
            case '': return alphabetPractice;
            case '': return numberPractice;
            case 'Caps Lock': return capsLockPractice;
            case '': return correctionPractice;
            case '': return alphabetChallengePractice;
            case '': return alphaNumTanbunChallengePractice;
            case '': return alphaSymbolPractice1;
            case '': return alphaSymbolPractice2;
            case '': // 
            case '_': // 
                return kigoPractice3SymbolMenu;
            default: return null;
        }
    }, [
        practice, gIdx, dIdx, // Add gIdx and dIdx to ensure activePractice is re-evaluated when they change
        sokuonKomojiPractice, kigoPractice1, kigoPractice2, kigoPractice3,
        youdakuonPractice, youhandakuonPractice, youonKakuchoPractice, gairaigoPractice, kigoPractice3SymbolMenu, // kigoPractice3SymbolMenu 
        kanaChallengePractice, kigoChallengePractice, tanbunChallengePractice, alphabetPractice,
        numberPractice, capsLockPractice, correctionPractice, alphaSymbolPractice1, alphaSymbolPractice2,
        alphabetChallengePractice, alphaNumTanbunChallengePractice
    ]);

    useEffect(() => {
        // activePractice  headingChars 
        if (activePractice?.status === 'finished') {
            setIsChallengeFinished(true);
        } else {
            setIsChallengeFinished(false);
        }
    }, [activePractice?.status]);

    const handlePracticeSelect = useCallback((item: PracticeMode) => {
        setPractice(item);
        setGIdx(0);
        if (item === '') {
            setDIdx(1);
        } else {
            setDIdx(0);
        }
        setIsRandomMode(false);
        if (activePractice && typeof activePractice.reset === 'function') {
            activePractice.reset();
        }
        setIsChallengeFinished(false);
    }, [activePractice, setPractice, setGIdx, setDIdx, setIsRandomMode, setIsChallengeFinished]);

    const toggleRandomMode = useCallback((): void => {
        const newIsRandomMode = !isRandomMode;
        setIsRandomMode(newIsRandomMode);
        setGIdx(0);
        if (!newIsRandomMode && practice === '') {
            setDIdx(1);
        } else {
            setDIdx(0);
        }
    }, [isRandomMode, practice, setIsRandomMode, setGIdx, setDIdx]);

    const headingChars = useMemo(() => {
        return activePractice?.headingChars ?? [];
        console.log(`[PracticeManagement headingChars useMemo] activePractice?.headingChars:`, activePractice?.headingChars);
    }, [activePractice?.headingChars]);

    const isRandomModeAvailable = useMemo(() => {
        const nonRandomModes: Array<PracticeMode | ''> = [
            '',
            '',
            '',
            '',
            '',
            'Caps Lock',
            '',
        ];
        return !nonRandomModes.includes(practice);
    }, [practice]);

    return useMemo(() => {
        return {
            practice,
            gIdx,
            dIdx,
            isRandomMode,
            activePractice,
            headingChars,
            isChallengeFinished,
            handlePracticeSelect,
            toggleRandomMode,
            setPractice,
            setGIdx,
            setDIdx,
            setIsRandomMode,
            nextStage,
            handleInput: activePractice?.handleInput ?? ((_info: PracticeInputInfo) => ({ isExpected: false, shouldGoToNext: false })),
            isRandomModeAvailable,
            showChallengeKeys, // Expose showChallengeKeys state
            toggleShowChallengeKeys, // Expose toggle function
            targetChar: activePractice?.targetChar, // targetChar 
            status: activePractice?.status,
            countdownValue: activePractice?.countdownValue,
            challengeResults: activePractice?.challengeResults,
            targetLayerIndex: activePractice?.targetLayerIndex,
            targetLayerIndices: activePractice?.targetLayerIndices,
            displayLayers: activePractice?.displayLayers,
            getHighlightClassName: activePractice?.getHighlightClassName ?? (() => ({ className: null, overrideKey: null })),
            isInvalidInputTarget: activePractice?.isInvalidInputTarget ?? (() => false),
            reset: activePractice?.reset,
        };
    }, [
        practice, gIdx, dIdx, isRandomMode, activePractice, headingChars,
        isChallengeFinished, handlePracticeSelect, toggleRandomMode,
        setPractice, setGIdx, setDIdx, setIsRandomMode, nextStage,
        isRandomModeAvailable, showChallengeKeys, toggleShowChallengeKeys // showChallengeKeys  toggleShowChallengeKeys 
    ]);
};

export default usePracticeManagement;
