import { ChallengeResult } from "../hooks/usePracticeCommons";

// ---  () ---
const getRankMessage = (score: number, accuracy: number): string => {
    // accuracy  0-1 
    const accuracyPercentage = accuracy * 100;

    if (score >= 5000 && accuracyPercentage >= 98) return "Typing Master! \n";
    if (score >= 4000 && accuracyPercentage >= 95) return "Excellent! \n";
    if (score >= 3000 && accuracyPercentage >= 90) return "Great! \n";
    if (score >= 2000 && accuracyPercentage >= 85) return "Good! \n";
    if (score >= 1000) return "Nice! \n";
    return "Keep Trying! ";
};

// ---  () ---
export const calculateChallengeScoreAndRank = (results: ChallengeResult): { score: number; rankMessage: string } => {
    // ChallengeResult :
    // totalQuestions:  ()
    // correctCharsCount:  ()
    // totalCharsTyped:  ()
    // correctCount:  ()
    // missCount: 
    // accuracy:  (0-1)
    // cpm: Characters Per Minute ()
    // maxConsecutiveCorrectChars: 

    // 
    // P_base:  * 10
    // M_acc: ^2
    // P_speed: max(0, cpm - 50) * 5
    // P_combo: log(1 + maxConsecutiveCorrectChars) * 100
    // P_task:  * 50 ( totalQuestions )

    const P_base = (results.correctCharsCount ?? results.correctCount) * 10; // 
    const M_acc = Math.max(0, Math.pow(results.accuracy, 2));
    const P_speed = Math.max(0, results.cpm - 50) * 5;
    const P_combo = Math.log(1 + (results.maxConsecutiveCorrectChars ?? 0)) * 100;
    const P_task = (results.totalQuestions ?? (results.correctCharsCount ?? results.correctCount)) * 50; // 

    const totalScore = Math.round((P_base + P_speed + P_combo + P_task) * M_acc);

    // 
    const rankMessage = (results.totalCharsTyped ?? 0) > 0 ? getRankMessage(totalScore, results.accuracy) : '';

    return {
        score: totalScore,
        rankMessage: rankMessage,
    };
};