// /home/coffee/my-keymap-viewer/src/hooks/useNumberPractice.ts
import { useState, useCallback, useMemo, useEffect, useRef } from 'react';
import {
    PracticeHookProps,
    PracticeHookResult,
    PracticeInputInfo,
    PracticeInputResult as HandleInputResult,
    PracticeStatus,
    PracticeHighlightResult,
} from './usePracticeCommons';

const HEADER_NUMBERS = "1234567890";

// 
const toHalfWidth = (str: string): string => {
    return str.replace(/[-]/g, (s) => String.fromCharCode(s.charCodeAt(0) - 0xFEE0));
};

const useNumberPractice = ({
    gIdx, // App.tsx  dIdx  gIdx 
    // dIdx, //  dIdx 
    isActive,
    onAdvance,
    isRandomMode, // isRandomMode  props 
    layers,
}: PracticeHookProps): PracticeHookResult => {
    // 'targetDigit' (1-9)  'targetZero' (0)
    const [internalStage, setInternalStage] = useState<'targetDigit' | 'targetZero'>('targetDigit');
    const [status, setStatus] = useState<PracticeStatus>('waiting');
    const [errorHighlightLayer, setErrorHighlightLayer] = useState<number | null>(null); // 

    const [showHighlightForSecondZero, setShowHighlightForSecondZero] = useState(true); // 2"0"
    const highlightDelayTimerRef = useRef<number | null>(null);
    const [randomTargetDigit, setRandomTargetDigit] = useState<string | null>(null); // 

    const prevIsActiveRef = useRef(isActive);
    const prevGIdxRef = useRef(gIdx);
    const prevIsRandomModeRef = useRef(isRandomMode);
    // console.log(`[NumberPractice RENDER] Hook initialized/rendered. isActive: ${isActive}, gIdx (prop): ${gIdx}, internalStage: ${internalStage}, status: ${status}`);

    const reset = useCallback(() => {
        console.log('[NumberPractice reset] Resetting states...');
        setInternalStage('targetDigit');
        setStatus('waiting');
        setErrorHighlightLayer(null); // 
        setRandomTargetDigit(null); // 
        setShowHighlightForSecondZero(true); // 
        if (highlightDelayTimerRef.current !== null) {
            clearTimeout(highlightDelayTimerRef.current);
            highlightDelayTimerRef.current = null;
        }
        // gIdx  usePracticeManagement  (props)
    }, [setInternalStage, setStatus, setShowHighlightForSecondZero]);

    const selectNextRandomDigit = useCallback(() => {
        const randomNumber = Math.floor(Math.random() * 10); // 0-9
        const digit = randomNumber.toString();
        setRandomTargetDigit(digit);
        //  '0'  'targetZero'  'targetDigit'
        //  'targetDigit' 
        // ( '0'  'targetDigit'  '0'  'targetZero'  '0' )
        // 1
        // selectNextRandomDigit  internalStage  'targetDigit' ( 'targetZero' if randomTargetDigit is '0')
        setInternalStage('targetDigit'); //  targetDigit currentTargetDigitChar 
        setStatus('waiting');
        setErrorHighlightLayer(null);
        setShowHighlightForSecondZero(true); // 
        if (highlightDelayTimerRef.current !== null) {
            clearTimeout(highlightDelayTimerRef.current);
            highlightDelayTimerRef.current = null;
        }
        console.log(`[NumberPractice selectNextRandomDigit] New random target: ${digit}`);
    }, []);

    useEffect(() => {
        console.log(`[NumberPractice useEffect START] isActive: ${isActive}, gIdx (prop): ${gIdx}, prevIsActive: ${prevIsActiveRef.current}, prevGIdx: ${prevGIdxRef.current}, current internalStage: ${internalStage}, current status: ${status}`);
        const randomModeChanged = isRandomMode !== prevIsRandomModeRef.current;

        if (!isActive && prevIsActiveRef.current) {
            console.log('[NumberPractice useEffect] Practice became inactive. Calling reset().');
            reset();
        } else if (isActive) {
            if (randomModeChanged) {
                reset(); // 
                if (isRandomMode) {
                    selectNextRandomDigit();
                } else {
                    // gIdx 
                    const currentTargetDigitChar = HEADER_NUMBERS[gIdx];
                    setRandomTargetDigit(null); // 
                    //  targetDigit 
                    setInternalStage('targetDigit');
                    setStatus('waiting'); // 
                    setShowHighlightForSecondZero(true); // 
                    if (highlightDelayTimerRef.current !== null) {
                        clearTimeout(highlightDelayTimerRef.current);
                        highlightDelayTimerRef.current = null;
                    }
                }
            } else if (isRandomMode) {
                if (isActive && !prevIsActiveRef.current || !randomTargetDigit) { // 
                    selectNextRandomDigit();
                }
            } else { // 
                // gIdx 
                if (gIdx !== prevGIdxRef.current || (isActive && !prevIsActiveRef.current)) {
                    const currentTargetDigitChar = HEADER_NUMBERS[gIdx];
                    console.log(`[NumberPractice useEffect Normal Mode] gIdx changed (from ${prevGIdxRef.current} to ${gIdx}) or practice just activated. Target char: ${currentTargetDigitChar}. Setting internalStage to 'targetDigit'.`);
                    // gIdx  targetDigit 
                    setInternalStage('targetDigit');
                    setErrorHighlightLayer(null); // 
                    setShowHighlightForSecondZero(true); // gIdx
                    if (highlightDelayTimerRef.current !== null) {
                        clearTimeout(highlightDelayTimerRef.current);
                        highlightDelayTimerRef.current = null;
                    }
                    setStatus('waiting');
                }
            }
        }
        prevIsActiveRef.current = isActive;
        prevGIdxRef.current = gIdx;
        prevIsRandomModeRef.current = isRandomMode;
        console.log(`[NumberPractice useEffect END] Updated refs: prevIsActive=${prevIsActiveRef.current}, prevGIdx=${prevGIdxRef.current}. Current internalStage: ${internalStage}, status: ${status}`);
    }, [gIdx, isActive, isRandomMode, reset, selectNextRandomDigit, randomTargetDigit]);

    const handleInput = useCallback((input: PracticeInputInfo): HandleInputResult => {
        console.log(`[NumberPractice handleInput START] Input pressCode: 0x${input.pressCode.toString(16)}, type: ${input.type}. Current state: isActive=${isActive}, internalStage=${internalStage}, gIdx=${gIdx}, status=${status}, isRandomMode=${isRandomMode}, randomTargetDigit=${randomTargetDigit}`);

        if (!isActive || !layers) {
            // console.log(`[NumberPractice handleInput] PRE-CONDITION FAILED. isActive: ${isActive}, layers: ${!!layers}`);
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        if (input.type !== 'release') {
            // console.log(`[NumberPractice handleInput] Not a 'release' event. Type: ${input.type}. Skipping.`);
            return { isExpected: false, shouldGoToNext: false };
        }

        const currentTargetDigitChar = isRandomMode
            ? randomTargetDigit
            : HEADER_NUMBERS[gIdx]; // 

        if (!currentTargetDigitChar) { // 
            return { isExpected: false, shouldGoToNext: false, errorLayer: null };
        }

        let expectedKeyName: string | null = null;

        //  ()
        if (internalStage === 'targetDigit') {
            expectedKeyName = currentTargetDigitChar;
        }
        // 2 ("0")
        else if (internalStage === 'targetZero') {
            expectedKeyName = "0"; //  "0" 
        }

        // 0
        let originalPressedKeyNameOnLayer0: string | null = null;
        let normalizedPressedKeyName: string | null = null;

        if (layers[0] && input.pressCode >= 0 && input.pressCode < layers[0].length) {
            // input.pressCode 0
            originalPressedKeyNameOnLayer0 = layers[0][input.pressCode]?.trim() ?? null;
            if (originalPressedKeyNameOnLayer0) {
                normalizedPressedKeyName = toHalfWidth(originalPressedKeyNameOnLayer0);
            }
            console.log(`[NumberPractice handleInput] Key Lookup: input.pressCode=${input.pressCode}, layers[0][${input.pressCode}]="${layers[0][input.pressCode]}". Original: "${originalPressedKeyNameOnLayer0}", Normalized: "${normalizedPressedKeyName}", Expected: "${expectedKeyName}"`);
        }


        console.log(`[NumberPractice handleInput BEFORE COMPARISON] expectedKeyName: "${expectedKeyName}", normalizedPressedKeyName: "${normalizedPressedKeyName}"`);

        if (expectedKeyName && normalizedPressedKeyName && normalizedPressedKeyName === expectedKeyName) {
            setStatus('correct');
            setErrorHighlightLayer(null);

            if (internalStage === 'targetDigit') {
                // 1
                console.log(`[NumberPractice handleInput] Target digit "${expectedKeyName}" (1st key) MATCH. Setting internalStage to 'targetZero'.`);
                if (highlightDelayTimerRef.current !== null) { // 
                    clearTimeout(highlightDelayTimerRef.current);
                    highlightDelayTimerRef.current = null;
                }
                setInternalStage('targetZero');
                if (expectedKeyName === '0') { // 1 "0" 
                    setShowHighlightForSecondZero(false);
                    highlightDelayTimerRef.current = window.setTimeout(() => {
                        setShowHighlightForSecondZero(true);
                        highlightDelayTimerRef.current = null;
                        console.log("[NumberPractice handleInput] Highlight delay for second '0' ended.");
                    }, 300);
                } else {
                    setShowHighlightForSecondZero(true); // "0" 
                }
                return { isExpected: true, shouldGoToNext: false, errorLayer: null }; 
            } else if (internalStage === 'targetZero') { // "0" 
                console.log(`[NumberPractice handleInput] Zero MATCH. Advancing practice.`);
                setShowHighlightForSecondZero(true); // 2
                if (highlightDelayTimerRef.current !== null) {
                    clearTimeout(highlightDelayTimerRef.current);
                    highlightDelayTimerRef.current = null;
                }
                if (isRandomMode) {
                    selectNextRandomDigit(); //  (internalStage  selectNextRandomDigit  targetDigit )
                    return { isExpected: true, shouldGoToNext: false, errorLayer: null }; //  App.tsx 
                } else { // 
                    setInternalStage('targetDigit'); //  targetDigit 
                    setStatus('waiting'); // 
                    return { isExpected: true, shouldGoToNext: true, errorLayer: null }; // App.tsx  dIdx 
                }
            }
        }
        console.log(`[NumberPractice handleInput AFTER COMPARISON / NO MATCH] Expected: "${expectedKeyName}", Pressed (normalized): "${normalizedPressedKeyName}". Setting status to 'incorrect'.`);
        setStatus('incorrect');
        setShowHighlightForSecondZero(true); // 
        if (highlightDelayTimerRef.current !== null) {
            clearTimeout(highlightDelayTimerRef.current);
            highlightDelayTimerRef.current = null;
        }
        setErrorHighlightLayer(0); // 0
        // 
        return { isExpected: false, shouldGoToNext: false, errorLayer: 0, errorPressCode: input.pressCode }; // errorPressCode 
    }, [
        isActive, internalStage, gIdx, onAdvance, layers, setInternalStage, setStatus,
        isRandomMode, randomTargetDigit, selectNextRandomDigit // 
    ]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => { // Added keyIndex
        // 
        if (errorHighlightLayer === layoutIndex) {
            return { className: null, overrideKey: null };
        }
        console.log(`[NumberPractice getHighlightClassName START] keyName: "${keyName}", layoutIndex: ${layoutIndex}, isActive: ${isActive}, internalStage: ${internalStage}, gIdx: ${gIdx}, isRandomMode: ${isRandomMode}, randomTargetDigit: ${randomTargetDigit}`);
        if (!isActive || layoutIndex !== 0) { // 0
            // console.log(`[NumberPractice getHighlightClassName] Not active or not layer 0. Returning no highlight.`);
            return { className: null, overrideKey: null };
        }

        const currentTargetCharForHighlight = isRandomMode
            ? randomTargetDigit
            : HEADER_NUMBERS[gIdx]; //  (: "1", "2")

        if (!currentTargetCharForHighlight) {
            return { className: null, overrideKey: null };
        }

        let expectedKeyToHighlight: string | null = null;

        const normalizedKeyName = toHalfWidth(keyName.trim()); // 
        console.log(`[NumberPractice getHighlightClassName] Normalized keyName: "${normalizedKeyName}" (original: "${keyName.trim()}"), currentTargetCharForHighlight: "${currentTargetCharForHighlight}"`);

        //  ()
        if (internalStage === 'targetDigit') {
            expectedKeyToHighlight = currentTargetCharForHighlight;
        }
        // 2 ("0")
        else if (internalStage === 'targetZero') {
            if (showHighlightForSecondZero) { // showHighlightForSecondZero  true  "0" 
                expectedKeyToHighlight = "0";
            }
        }

        // 
        if (expectedKeyToHighlight && normalizedKeyName === expectedKeyToHighlight) {
            console.log(`[NumberPractice getHighlightClassName] MATCH: Normalized key "${normalizedKeyName}" === Expected key "${expectedKeyToHighlight}". Returning highlight.`);
            return { className: 'bg-blue-100', overrideKey: null };
        }
        // console.log(`[NumberPractice getHighlightClassName] No highlight. internalStage: ${internalStage}, expectedKeyToHighlight: ${expectedKeyToHighlight}, showHighlightForSecondZero: ${showHighlightForSecondZero}`);

        console.log(`[NumberPractice getHighlightClassName] NO MATCH: Normalized key "${normalizedKeyName}", Expected key to highlight: "${expectedKeyToHighlight}". Returning no highlight.`);
        return { className: null, overrideKey: null };
    }, [isActive, internalStage, gIdx, isRandomMode, randomTargetDigit, errorHighlightLayer, showHighlightForSecondZero]); // Add showHighlightForSecondZero

    const headingChars = useMemo(() => {
        if (isRandomMode) return randomTargetDigit ? [randomTargetDigit] : [];
        return HEADER_NUMBERS.split('');
    }, [isRandomMode, randomTargetDigit]);

    const isInvalidInputTarget = useCallback((targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number, pressedLayerIndex?: number): boolean => {
        if (!isActive || status !== 'incorrect') return false;

        // pressedLayerIndex  currentLayoutIndex 
        // 0
        if (pressedLayerIndex !== undefined) {
            return currentLayoutIndex === pressedLayerIndex && currentLayoutIndex === 0 && currentKeyIndex === targetPressCode;
        }
        // pressedLayerIndex  ()
        return currentLayoutIndex === 0 && currentKeyIndex === targetPressCode;
    }, [isActive, status]);

    const clearErrorState = useCallback(() => {
        setErrorHighlightLayer(null);
        setShowHighlightForSecondZero(true); // 
        if (highlightDelayTimerRef.current !== null) {
            clearTimeout(highlightDelayTimerRef.current);
            highlightDelayTimerRef.current = null;
        }
        setStatus('waiting'); //  waiting 
    }, [setErrorHighlightLayer, setStatus, setShowHighlightForSecondZero]);

    const currentTargetForHook = isRandomMode ? randomTargetDigit : HEADER_NUMBERS[gIdx];

    return {
        status, // status 
        handleInput,
        getHighlightClassName,
        currentTarget: currentTargetForHook || undefined, // 
        targetChar: currentTargetForHook || undefined, // targetChar 
        reset,
        targetLayerIndex: null, // 
        headingChars,
        targetLayerIndices: [0], //  (0) 
        isInvalidInputTarget,
        errorHighlightLayer, // 
        clearErrorState,     // 
    };
};

export default useNumberPractice;
