import React, { useCallback } from 'react'; // useState, useRef, useEffect 
import { motion } from 'framer-motion';
import { PracticeMode, PracticeHookResult, PracticeHighlightResult } from '../hooks/usePracticeCommons';
import { kigoMapping2, kigoMapping3, EQUAL_KIGO_KEY_NAME } from '../data/keymapData'; // EQUAL_KIGO_KEY_NAME 
import { getKeyStyle, isLargeSymbol, isSpecialSymbolLayer8, isSpecialSymbolLayer7, isSpecialSymbolLayer6, isSpecialSymbolLayer9, isSpecialSymbolLayer10 } from '../utils/styleUtils';

interface KeyboardLayoutProps {
  layerData: string[] | undefined;
  layoutIndex: number;
  layoutTitle: string;
  cols: number;
  fixedWidth: string;
  showKeyLabels: boolean;
  lastInvalidLayerIndex: number | null; // 
  lastInvalidKeyCode: number | null;
  activePractice: PracticeHookResult | null;
  isInvalidInputTarget: (targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number) => boolean; // App.tsx 
  practice: PracticeMode;
  currentFunctionKeyMap: Record<number, string>;
  training: boolean;
  className?: string;
  // clearInvalidHighlight?: () => void; // App.tsx
}

const KeyboardLayout: React.FC<KeyboardLayoutProps> = ({
  layerData,
  layoutIndex,
  layoutTitle,
  cols,
  fixedWidth,
  showKeyLabels,
  lastInvalidLayerIndex, // 
  lastInvalidKeyCode,
  activePractice,
  practice,
  currentFunctionKeyMap,
  training,
  className = '',
  isInvalidInputTarget, // props 
  // clearInvalidHighlight, // App.tsx
}) => {
    //  invalidKeyIndex  invalidTimerRef 

    // useEffect  (lastInvalidKeyCode  App.tsx )

    //  (renderKey)
    const renderKey = useCallback((key: string, idx: number) => {
        let originalKey = (key ?? '').trim();
        const isEmptyKey = originalKey === '';

        let k = originalKey; //  ()
        let highlightResult: PracticeHighlightResult = { className: null, overrideKey: null }; // highlightResult 
        // App.tsx  isInvalidInputTarget 
        let isInvalid = isInvalidInputTarget(idx, layoutIndex, idx); // isInvalid 
        //  App.tsx  lastInvalidKeyCode  lastInvalidLayerIndex 
        // targetPressCode (1)  idx 
        // if (isInvalidInputTarget(idx, layoutIndex, idx)) {
            // console.log(`[KeyboardLayout renderKey IS_INVALID_CHECK] isInvalidInputTarget (from props) returned TRUE for key ${originalKey} (idx ${idx}) on layer ${layoutIndex}`);
        //     isInvalid = true;
        // } else {
            // console.log(`[KeyboardLayout renderKey IS_INVALID_CHECK] isInvalidInputTarget (from props) returned FALSE for key "${originalKey}" (idx ${idx}) on layer ${layoutIndex}`);
        // }

        // ON (kigoMapping )
        if (training && showKeyLabels) {
            const funcKeyLabel = currentFunctionKeyMap[idx];

            // 
            if (practice === '' && layoutIndex === 3 && ['', ''].includes(originalKey)) {
                 k = originalKey; // 
            } else
            if (practice === '') {
                if (layoutIndex === 6) { // 1
                    if (isEmptyKey && funcKeyLabel) {
                        k = funcKeyLabel;
                    }
                } else if (layoutIndex === 2) { // 
                     k = '____';
                }
            } else if (practice === '') {
                if (layoutIndex === 7) {
                    if (funcKeyLabel) { // 
                        k = funcKeyLabel;
                    }
                    //  (kigoMapping2 )
                } else if (layoutIndex === 2) { // 
                    k = '____';
                }
            } else if (practice === '') {
                if (layoutIndex === 8) {
                    if (funcKeyLabel) { // 
                        k = funcKeyLabel;
                    }
                    //  (kigoMapping3 )
                } else if (layoutIndex === 2) { // 
                    k = '____';
                }
            } else if (layoutIndex === 3) { // 
                //  ( ____ )
                if (['1', '2', '3', '4'].includes(originalKey)) { // 
                    k = '____';
                }
            } else if (layoutIndex === 2) { //  ()
                 if (funcKeyLabel) {
                     k = funcKeyLabel;
                 }
            }
        }

        //  overrideKey 
        if (showKeyLabels && activePractice && typeof activePractice.getHighlightClassName === 'function') {
            console.log(`[KeyboardLayout] PRE-CALL getHighlightClassName for key: "${originalKey}", layoutIndex: ${layoutIndex}, keyIndex: ${idx}, activePractice.status: ${activePractice.status}, activePractice.targetChar: ${activePractice.targetChar}, currentTarget (if available):`, activePractice.currentTarget);
            try {
                highlightResult = activePractice.getHighlightClassName(originalKey, layoutIndex, idx);
            } catch (e) {
                console.error(`[KeyboardLayout] ERROR calling getHighlightClassName for key "${originalKey}" on layer ${layoutIndex}:`, e);
            }
        }

        // 
        let displayContent: string;
        if (!training) { // training  false OFF
            // OFF
            let keyStyleClass = ''; // 
            //  (Tailwind CSS)
            const standardKeyBgColor = 'bg-white dark:bg-gray-700';
            const standardKeyTextColor = 'text-gray-900 dark:text-gray-100';

            let fontSizeClass = '';
            // Determine if the key is a special symbol on layer 8 before the if/else if block for fontSizeClass
            const isKeySpecialSymbolLayer8 = layoutIndex === 8 && isSpecialSymbolLayer8(originalKey);
            const isKeySpecialSymbolLayer7 = layoutIndex === 7 && isSpecialSymbolLayer7(originalKey);
            const isKeySpecialSymbolLayer6 = layoutIndex === 6 && isSpecialSymbolLayer6(originalKey);
            const isKeySpecialSymbolLayer9 = layoutIndex === 9 && isSpecialSymbolLayer9(originalKey); // 9
            const isKeySpecialSymbolLayer10 = layoutIndex === 10 && isSpecialSymbolLayer10(originalKey); // 10

            if (isLargeSymbol(originalKey)) {
                fontSizeClass = ' text-3xl'; // 1.8rem  text-3xl 
            } else if (isKeySpecialSymbolLayer8) { // Use the pre-calculated boolean
                fontSizeClass = ' text-2xl'; // 
            } else if (isKeySpecialSymbolLayer7) {
                fontSizeClass = ' text-2xl'; // 7 (8)
            } else if (isKeySpecialSymbolLayer6) {
                fontSizeClass = ' text-2xl'; // 6
            } else if (isKeySpecialSymbolLayer9) {
                fontSizeClass = ' text-2xl'; // 9
            } else if (isKeySpecialSymbolLayer10) {
                fontSizeClass = ' text-2xl'; // 10
            }
            if (layoutIndex === 8) { // Log for all layer 8 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 8 Key: "${originalKey}", isSpecialSymbolLayer8 evaluated to: ${isKeySpecialSymbolLayer8}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 7) { // Log for all layer 7 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 7 Key: "${originalKey}", isSpecialSymbolLayer7 evaluated to: ${isKeySpecialSymbolLayer7}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 6) { // Log for all layer 6 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 6 Key: "${originalKey}", isSpecialSymbolLayer6 evaluated to: ${isKeySpecialSymbolLayer6}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 6) { // Log for all layer 6 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 6 Key: "${originalKey}", isSpecialSymbolLayer6 evaluated to: ${isKeySpecialSymbolLayer6}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 9) { // Log for all layer 9 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 9 Key: "${originalKey}", isSpecialSymbolLayer9 evaluated to: ${isKeySpecialSymbolLayer9}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 10) { // Log for all layer 10 keys in !training mode
                console.log(`[KeyboardLayout renderKey !training] Layer 10 Key: "${originalKey}", isSpecialSymbolLayer10 evaluated to: ${isKeySpecialSymbolLayer10}, Calculated fontSizeClass: "${fontSizeClass}"`);
            }
            if (layoutIndex === 8 && originalKey === EQUAL_KIGO_KEY_NAME) {
                console.log(`[KeyboardLayout renderKey !training] Layer 8 "\\n" key. Applying standardKeyBgColor and standardKeyTextColor directly.`);
                // \nOFF
                // getKeyStyle 
                keyStyleClass = `${standardKeyBgColor} ${standardKeyTextColor}`;
            } else if ([2, 7, 8, 9, 10].includes(layoutIndex) && originalKey === '') {
                console.log(`[KeyboardLayout renderKey !training] Layer ${layoutIndex} "" key. Applying standardKeyBgColor and standardKeyTextColor directly.`);
                // 2, 7, 8, 9, 10 
                // getKeyStyle 
                keyStyleClass = `${standardKeyBgColor} ${standardKeyTextColor}`;
            } else {
                // 
                keyStyleClass = getKeyStyle(originalKey);
                // getKeyStyle 
                console.log(`[KeyboardLayout renderKey !training] For key "${originalKey}" on layer ${layoutIndex}, getKeyStyle returned: "${keyStyleClass}"`);
                if (!keyStyleClass.includes('text-')) {
                     keyStyleClass = `${keyStyleClass} ${standardKeyTextColor}`.trim();
                }
                if (!keyStyleClass.includes('bg-')) { // getKeyStyle 
                    keyStyleClass = `${keyStyleClass} ${standardKeyBgColor}`.trim();
                }
                // 
            }

            return (
                <motion.div key={idx} className={`border rounded p-3 text-center shadow flex justify-center items-center ${keyStyleClass} ${fontSizeClass || 'text-sm'}`}
                    whileHover={{ scale: 1.05 }} style={{ minHeight: '3rem' }}>
                    <code style={{ whiteSpace: "pre-line" }}>
                      {(originalKey ?? "").split(/\r?\n/).map((l: string, i: number, a: string[]) => (
                        <span key={i}>
                          {l}
                          {i < a.length - 1 && <br />}
                        </span>
                      ))}
                    </code>
                </motion.div>
            );
        }

        // ON
        let fontSizeClassForTraining = "";
        if (isLargeSymbol(originalKey)) {
            fontSizeClassForTraining = ' text-3xl';
        } else if (layoutIndex === 8 && isSpecialSymbolLayer8(originalKey)) { // 8
            fontSizeClassForTraining = " text-2xl"; //  text-2xl 
        } else if (layoutIndex === 6 && isSpecialSymbolLayer6(originalKey)) { // 6
            fontSizeClassForTraining = " text-2xl"; //  text-2xl 
        } else if (layoutIndex === 7 && isSpecialSymbolLayer7(originalKey)) { // 7
            fontSizeClassForTraining = " text-2xl"; //  text-2xl 
        } else if (layoutIndex === 10 && isSpecialSymbolLayer10(originalKey)) { // 10
            fontSizeClassForTraining = " text-2xl"; //  text-2xl 
        } else if (layoutIndex === 9 && isSpecialSymbolLayer9(originalKey)) { // 9
            fontSizeClassForTraining = " text-2xl"; //  text-2xl 
        }

        // ON
        if (showKeyLabels) {
            const finalKeyLabel = highlightResult.overrideKey ?? k;
            displayContent = finalKeyLabel === '' ? '\n' : finalKeyLabel;

            // \n
            if (practice === '' && originalKey === EQUAL_KIGO_KEY_NAME) {
                displayContent = EQUAL_KIGO_KEY_NAME;
            }
        } else {
            // OFF ____ 
            displayContent = isEmptyKey ? '\n' : '____';
        }

        // ON
        const trainingBaseKeyClasses = "border rounded p-3 text-center shadow flex justify-center items-center whitespace-pre-line";
        const finalClassName = `${trainingBaseKeyClasses} ${highlightResult.className ?? ''} ${isInvalid ? 'debug-invalid-key' : ''} ${fontSizeClassForTraining || 'text-sm'}`;
        if (originalKey === '' && layoutIndex === 2 && training) { // defaultTargetLayerIndex is 2
            console.log(`[KeyboardLayout "" FINAL_CLASS_CHECK] finalClassName: "${finalClassName}", highlightResult.className: "${highlightResult.className}"`);
        }
        return (
            <div
              key={idx}
              className={finalClassName}
              style={{ minHeight: '3rem', backgroundColor: isInvalid ? 'rgba(254, 202, 202, 0.5)' : undefined }} //  ()
            >
                {displayContent}
            </div>
        );
    }, [
        layoutIndex, showKeyLabels, activePractice, practice,
        currentFunctionKeyMap,
        training,
        lastInvalidKeyCode, // lastInvalidKeyCode 
        lastInvalidLayerIndex, // lastInvalidLayerIndex 
            isInvalidInputTarget, // 
    ]);

    if (!layerData) {
        return null; // 
    }

    // 
    return (
        <div className={`keyboard-layout bg-gray-100 p-2 rounded shadow-md ${className}`} style={{ width: fixedWidth }}>
            <h2 className="text-center font-semibold mb-2 text-sm">{layoutTitle}</h2>
            <div className={`grid gap-1`} style={{ gridTemplateColumns: `repeat(${cols}, minmax(0, 1fr))` }}>
                {layerData.map((key, index) => renderKey(key, index))}
            </div>
        </div>
    );
};

export default KeyboardLayout;
