import {
    PracticeHookProps,
    PracticeInputInfo,
    PracticeInputResult,
    PracticeHookResult,
    PracticeHighlightResult,
    HighlightInfo,
    hid2Gyou,
    hid2Dan,
    functionKeyMaps,
    ChallengeResult,
    getHidKeyCodes, // 
    gyouChars,
    gyouList,      // Import gyouList
    danList,
    youonGyouList,
    youonGyouChars,
    youonDanMapping,
    dakuonGyouChars,
    dakuonDanMapping,
    handakuonGyouChars,
    handakuonDanMapping,
    youdakuonGyouChars,
    youdakuonDanMapping,
    youhandakuonGyouChars,
    allYoudakuonCharInfos, // Add this
    allYouhandakuonCharInfos, // Add this
    youhandakuonDanMapping,
    hid2Youon,
    KanaTargetInfo, // Import KanaTargetInfo
    EQUAL_KIGO_KEY_NAME, // Import EQUAL_KIGO_KEY_NAME
} from './usePracticeCommons';
import { tanbunSentences, kigoMapping3, kigoMapping2 } from '../data/keymapData'; //  kigoMapping3, kigoMapping2 

type ChallengeStatus = 'idle' | 'countdown' | 'running' | 'finished';

type TanbunProgressInfo = {
    typedEndIndex: number; // 
};


type InputState = {
    gyouKey: string | null; // 
    hasYouon: boolean;
    hasDakuon: boolean;
    hasHandakuon: boolean;
};
const initialInputState: InputState = { gyouKey: null, hasYouon: false, hasDakuon: false, hasHandakuon: false };


const COUNTDOWN_SECONDS = 5;
const INPUT_TIMEOUT_MS = 1500; // 1.5 ()
const TRAINING_DURATION_MS = 3 * 60 * 1000; // 3
const CHALLENGE_DURATION_SECONDS = 180; // 

const PUNCTUATION_STEP_1_GYOU_KEY = 'PUNCTUATION_STEP_1'; // 1
// const PUNCTUATION_STEP_1_KEY_NAME = '\n'; // 1 (EQUAL_KIGO_KEY_NAME )

//  (useKanaChallengePractice )
const getCurrentKanaTargetInfo = (char: string, layers: string[][]): KanaTargetInfo | null => {
    console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Called with char: '${char}'`);
    if (!char) {
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Char is null or empty, returning null.`);
        return null;
    }

    // Priority 1: Check for multi-character units first
    const foundYoudakuon = allYoudakuonCharInfos.find(info => info.char === char);
    if (foundYoudakuon) {
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Youdakuon (from allYoudakuonCharInfos):`, foundYoudakuon);
        return foundYoudakuon;
    }

    const foundYouhandakuon = allYouhandakuonCharInfos.find(info => info.char === char);
    if (foundYouhandakuon) {
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Youhandakuon (from allYouhandakuonCharInfos):`, foundYouhandakuon);
        return foundYouhandakuon;
    }

    // Priority 2: Simpler single-character kana types (youon, dakuon, handakuon, seion)
    // Youon (e.g., , )
    // This check is for full youon characters. getActualCurrentTargetDetails handles forming them.
        for (const gyou of youonGyouList) {
        if (youonGyouChars[gyou]?.includes(char)) {
            const danIndex = youonGyouChars[gyou]?.indexOf(char);
            if (danIndex !== -1 && danIndex !== undefined) { // Check for undefined index
                const result: KanaTargetInfo = { type: 'youon', char, gyouKey: gyou, danKey: youonDanMapping[gyou]?.[danIndex] ?? '' };
                console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Youon:`, result);
                return result;
            }
        }
    }
    // Dakuon
    for (const gyou of Object.keys(dakuonGyouChars)) {
        if (dakuonGyouChars[gyou]?.includes(char)) {
            const danIndex = dakuonGyouChars[gyou]?.indexOf(char);
            if (danIndex !== -1 && danIndex !== undefined) { // Check for undefined index
                const result: KanaTargetInfo = { type: 'dakuon', char, gyouKey: gyou, danKey: dakuonDanMapping[gyou]?.[danIndex] ?? '' };
                console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Dakuon:`, result);
                return result;
            }
        }
    }
    // Handakuon
    if (handakuonGyouChars['']?.includes(char)) {
        const gyou = '';
        const danIndex = handakuonGyouChars[gyou]?.indexOf(char);
        if (danIndex !== -1 && danIndex !== undefined) {
            const result: KanaTargetInfo = { type: 'handakuon', char, gyouKey: gyou, danKey: handakuonDanMapping[gyou]?.[danIndex] ?? '' };
            console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Handakuon:`, result);
            return result;
        }
    }

    // 
    for (const gyou of gyouList) {
        if (gyouChars[gyou]?.includes(char)) {
            const danIndex = gyouChars[gyou]?.indexOf(char);
            if (danIndex !== -1 && danIndex !== undefined) { // Check for undefined index
                let actualDanKey = '';
                if (gyou === '') { // 
                    if (char === '') actualDanKey = '';
                    else if (char === '') actualDanKey = '';
                    else if (char === '') actualDanKey = '';
                } else {
                    actualDanKey = danList[danIndex] ?? '';
                }
                if (actualDanKey) { // Ensure danKey is found
                    const result: KanaTargetInfo = { type: 'seion', char, gyouKey: gyou, danKey: actualDanKey };
                    console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Seion:`, result);
                    return result;
                }
            }
        }
    }

    // Priority 4: Kigo and Sokuon (single characters)
    if (char === '') {
        const result: KanaTargetInfo = { type: 'sokuonKomoji', char, isTsu: true };
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Sokuon '':`, result);
        return result;
    }
    // Handle single-character symbols that might be on layer 0, 7, or 8
    // Check layer 0 first for common symbols like ''
    if (char === '') {
        const result: KanaTargetInfo = { type: 'kigo', char, keyName: '', layerIndex: 0 }; // Assuming '' is on layer 0
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Kigo '':`, result);
        return result;
    }
    // Check layer 8 for symbols like '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''
    // Note: '' and '' are special cases handled by multi-key sequences in this practice.
    // Other symbols like '', '', etc., might be single-key presses on layer 8.
    const layer8 = layers[8];
    if (layer8 && layer8.includes(char)) {
         const result: KanaTargetInfo = { type: 'kigo', char, keyName: char, layerIndex: 8 };
         console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Kigo '${char}' on layer 8:`, result);
         return result;
    }
    if (char === '') {
        const result: KanaTargetInfo = { type: 'kigo', char, keyName: '', layerIndex: 8 }; // Assuming '' sequence involves layer 8
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Kigo '':`, result);
        return result;
    }
    if (char === '') {
        const result: KanaTargetInfo = { type: 'kigo', char, keyName: '', layerIndex: 8 }; // Assuming '' sequence involves layer 8
        console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Found Kigo '':`, result);
        return result;
    }

    // If no specific type is matched, return as 'unknown'
    console.log(`[TanbunChallenge getCurrentKanaTargetInfo] Char '${char}' not matched by specific types, returning as 'unknown'.`);
    return { type: 'unknown', char };
};

// Helper function to get the actual current target character and its info,
// considering multi-character Kana units.
const getActualCurrentTargetDetails = (
    sentence: string,
    index: number,
    layers: string[][] // Pass layers here
): { char: string; info: KanaTargetInfo | null } => {
    if (!sentence || index >= sentence.length) {
        return { char: '', info: null };
    }
    const char1 = sentence[index];
    let actualChar = char1; // Pass layers
    let actualInfo = getCurrentKanaTargetInfo(char1, layers); // Default to single char

    if (index + 1 < sentence.length) {
        const char2 = sentence[index + 1];
        const twoCharCandidate = char1 + char2;
        const twoCharInfo = getCurrentKanaTargetInfo(twoCharCandidate, layers); // Pass layers
        // Check if the two-character sequence is a valid Kana unit we want to treat as a single target (e.g., , , )
        if (twoCharInfo && (twoCharInfo.type === 'youon' || twoCharInfo.type === 'youdakuon' || twoCharInfo.type === 'youhandakuon')) {
            actualChar = twoCharCandidate;
            actualInfo = twoCharInfo;
        }
    }
    return { char: actualChar, info: actualInfo };
};
// layers  getActualCurrentTargetDetails 
const useTanbunChallengePractice = ({
    isActive,
    kb,
    side,
    layers, // layers 
    showKeyLabels, // Destructure showKeyLabels from props
}: PracticeHookProps): PracticeHookResult => {
    const [status, setStatus] = useState<ChallengeStatus>('idle');
    const [countdownValue, setCountdownValue] = useState<number>(COUNTDOWN_SECONDS);
    const [remainingTime, setRemainingTime] = useState<number>(TRAINING_DURATION_MS);
    const [currentSentence, setCurrentSentence] = useState<string>('');
    const [currentIndex, setCurrentIndex] = useState<number>(0); // 
    const [remainingSentences, setRemainingSentences] = useState<string[]>([]);
    const [inputState, setInputState] = useState<InputState>(initialInputState);
    const [progressInfo, setProgressInfo] = useState<TanbunProgressInfo>({ typedEndIndex: 0 });
    const [currentTargetChar, setCurrentTargetChar] = useState<string | null>(null); // 

    const countdownTimerRef = useRef<number | null>(null);
    const trainingTimerRef = useRef<number | null>(null);
    const startTimeRef = useRef<number | null>(null);
    const correctCountRef = useRef(0); // 
    const missCountRef = useRef(0); // 
    const totalCharsTypedRef = useRef(0); // 
    const completedCharsCountRef = useRef(0); // 
    const completedSentenceCountRef = useRef(0); // 
    const [challengeResults, setChallengeResults] = useState<ChallengeResult | null>(null);
    const inputTimeoutRef = useRef<number | null>(null);
    const prevIsActiveRef = useRef(isActive); // isActive 
    const currentSentenceRef = useRef<string>(''); //  ref 

    console.log(`[TanbunChallenge RENDER] isActive: ${isActive}, status: ${status}, currentIndex: ${currentIndex}, currentTargetChar: ${currentTargetChar}, inputState:`, JSON.parse(JSON.stringify(inputState)));


    //  ( - useKanaChallengePractice )
    const funcKeyCodes = useMemo(() => {
        const map = functionKeyMaps[kb]?.[side] ?? {};
        const findCode = (name: string) => {
            const entry = Object.entries(map).find(([_, n]) => n === name);
            return entry ? parseInt(entry[0]) + 1 : null;
        };
        return {
            youon: findCode(''),
            dakuon: findCode('  '),
            handakuon: findCode(''),
            sokuon: findCode(''),
            kigo: findCode(''), // 
        };
    }, [kb, side]);

    //  pressCode 
    // pressCode  0-based index
    const singleCharKeyCodes = useMemo(() => {
        const codes: Record<string, number | null> = {};
        // 
        const targetChars = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']; // Add more symbols

        // 0 () 
        const layer0 = layers[0];
        if (layer0) {
            targetChars.forEach(char => {
                const index = layer0.indexOf(char);
                if (index !== -1) {
                    codes[char] = index;
                }
            });
        }

        // 8 () 
        // Note: Some symbols like '\n' are multi-line key labels
        const layer8 = layers[8];
        if (layer8 && layer8.length > 0) {
            targetChars.forEach(char => {
                // \n
                const keyNameInLayout = char === '' ? EQUAL_KIGO_KEY_NAME : char;
                const index = layer8.indexOf(keyNameInLayout);
                if (index !== -1) {
                    codes[char] = index;
                }
            });
        }

        // 7 () 
        // Note: Some symbols might be on layer 7 (e.g. kigoMapping2)
        const layer7 = layers[7];
        if (layer7 && layer7.length > 0) {
             targetChars.forEach(char => {
                const index = layer7.indexOf(char);
                if (index !== -1) codes[char] = index;
            });
        }
        console.log("[TanbunChallenge singleCharKeyCodes calculated]", codes);
        return codes;
    }, [kb, side, layers]); // layers 

    const clearInputTimeout = useCallback(() => {
        if (inputTimeoutRef.current !== null) {
            console.log(`[TanbunChallenge clearInputTimeout] Clearing timeout ID: ${inputTimeoutRef.current}`);
            clearTimeout(inputTimeoutRef.current);
            inputTimeoutRef.current = null;
        }
    }, []);

    const resetInputStateAndClearTimeout = useCallback(() => {
        console.log(`[TanbunChallenge resetInputStateAndClearTimeout] Resetting inputState to initial and clearing timeout.`);
        setInputState(initialInputState);
        clearInputTimeout();
    }, [clearInputTimeout]);

    // 
    const selectNextSentence = useCallback(() => {
        console.log(`[TanbunChallenge selectNextSentence] Called. Remaining sentences: ${remainingSentences.length}`);
        if (remainingSentences.length > 0) {
            const randomIndex = Math.floor(Math.random() * remainingSentences.length);
            const nextSentence = remainingSentences[randomIndex];
            console.log(`[TanbunChallenge selectNextSentence] Selected new sentence: "${nextSentence}"`);
            setCurrentSentence(nextSentence);
            currentSentenceRef.current = nextSentence; // ref 
            setCurrentIndex(0);
            setCurrentTargetChar(nextSentence[0]); // 
            setProgressInfo({ typedEndIndex: 0 });
            // 
            setRemainingSentences(prev => {
                const newRemaining = prev.filter((_, i) => i !== randomIndex);
                return newRemaining;
            });
        } else {
            console.log(`[TanbunChallenge selectNextSentence] No more sentences. Setting to ""`);
            setCurrentSentence("");
            setCurrentIndex(0); // currentIndex typedEndIndex 
            setCurrentTargetChar(null); // 
            // setProgressInfo({ typedEndIndex: 0 }); // 
            // 
            // setStatus('finished');
        }
        clearInputTimeout();
    }, [remainingSentences, clearInputTimeout, setCurrentTargetChar]);

    // 
    useEffect(() => {
        console.log(`[TanbunChallenge useEffect Countdown] isActive: ${isActive}, status: ${status}, countdownValue: ${countdownValue}`);
        if (isActive && status === 'countdown') {
            if (countdownValue > 0) {
                countdownTimerRef.current = window.setTimeout(() => {
                    setCountdownValue(prev => prev - 1);
                }, 1000);
            } else {
                console.log(`[TanbunChallenge useEffect Countdown] Countdown finished. Setting status to 'running'.`);
                setStatus('running');
                setRemainingTime(TRAINING_DURATION_MS);
                startTimeRef.current = Date.now();
                setRemainingSentences([...tanbunSentences]); // Initialize remaining sentences
            }
        }
        return () => {
            if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        };
    }, [isActive, status, countdownValue]); // Removed selectNextSentence from deps, it's called from the next effect

    // Effect to select the first sentence when status becomes 'running' and no sentence is set
    useEffect(() => {
        console.log(`[TanbunChallenge useEffect SelectFirstSentence] status: ${status}, remainingSentences.length: ${remainingSentences.length}, currentSentence: "${currentSentence}"`);
        if (status === 'running' && remainingSentences.length > 0 && !currentSentence) {
            console.log(`[TanbunChallenge useEffect SelectFirstSentence] Conditions met, calling selectNextSentence.`);
            selectNextSentence();
        }
    }, [status, remainingSentences, currentSentence, selectNextSentence]);


    //  & 
    useEffect(() => {
        console.log(`[TanbunChallenge useEffect TrainingTimer] isActive: ${isActive}, status: ${status}`);
        if (isActive && status === 'running') {
            trainingTimerRef.current = window.setInterval(() => {
                const elapsed = Date.now() - (startTimeRef.current ?? Date.now());
                const newRemaining = Math.max(0, TRAINING_DURATION_MS - elapsed);
                setRemainingTime(newRemaining);
                if (newRemaining === 0) {
                    console.log(`[TanbunChallenge useEffect TrainingTimer] Time's up! Setting status to 'finished'.`);
                    setStatus('finished');
                    if (trainingTimerRef.current) clearInterval(trainingTimerRef.current);

                    const correct = completedCharsCountRef.current; // 
                    const miss = missCountRef.current;
                    const totalTyped = totalCharsTypedRef.current; // 
                    // : ( - ) / 
                    const accuracy = totalTyped > 0 ? Math.max(0, (totalTyped - miss) / totalTyped) : 0;
                    const durationMinutes = (TRAINING_DURATION_MS / 1000) / 60;
                    const cpmValue = durationMinutes > 0 ? Math.round(correct / durationMinutes) : 0;

                    const results: ChallengeResult = {
                        totalQuestions: completedSentenceCountRef.current, // 
                        totalCharsTyped: totalTyped, // 
                        correctCharsCount: correct, // 
                        correctCount: correct, // correctCount 
                        missCount: miss,
                        accuracy: accuracy,
                        score: 0, // Score will be calculated in PracticeView
                        cpm: cpmValue,
                        rankMessage: '', // Rank will be calculated in PracticeView
                    };
                    console.log(`[TanbunChallenge useEffect TrainingTimer] Challenge finished. Results:`, results);
                    setChallengeResults(results);
                }
            }, 100);
        } else if (status === 'finished' && trainingTimerRef.current) {
             clearInterval(trainingTimerRef.current);
        }
        return () => {
            if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        };
    }, [isActive, status]);

    // 
    const reset = useCallback(() => {
        console.log(`[TanbunChallenge reset] Called.`);
        setStatus('idle');
        setCountdownValue(COUNTDOWN_SECONDS);
        setRemainingTime(TRAINING_DURATION_MS);
        setCurrentSentence('');
        currentSentenceRef.current = ''; // ref 
        setCurrentIndex(0);
        setCurrentTargetChar(null); // 
        setRemainingSentences([]);
        setProgressInfo({ typedEndIndex: 0 });
        setChallengeResults(null);
        startTimeRef.current = null;
        setInputState(initialInputState);
        correctCountRef.current = 0; //  ()
        missCountRef.current = 0;
        totalCharsTypedRef.current = 0;
        completedCharsCountRef.current = 0; // 
        completedSentenceCountRef.current = 0;
        if (countdownTimerRef.current !== null) clearTimeout(countdownTimerRef.current);
        if (trainingTimerRef.current !== null) clearInterval(trainingTimerRef.current);
        clearInputTimeout();
    }, [clearInputTimeout, setCurrentTargetChar]);

    // 
    useEffect(() => {
        console.log(`[TanbunChallenge useEffect ActiveState] isActive: ${isActive}, prevIsActiveRef.current: ${prevIsActiveRef.current}, status: ${status}`);
        // isActive  false 
        if (!isActive && prevIsActiveRef.current) {
            console.log(`[TanbunChallenge useEffect ActiveState] Resetting state because isActive became false.`);
            reset();
        } else if (isActive && status === 'idle') { // isActive  true  status  idle 
            console.log(`[TanbunChallenge useEffect ActiveState] Practice is active and status is idle. Setting status to 'countdown'.`);
            setStatus('countdown');
        }
        prevIsActiveRef.current = isActive; // 
    }, [isActive, status, reset, clearInputTimeout]);

    // 
    const handleInput = useCallback((inputInfo: PracticeInputInfo): PracticeInputResult => {
        console.log(`[TanbunChallenge handleInput ENTRY] inputInfo:`, inputInfo, `status: ${status}, currentSentence: "${currentSentenceRef.current}", currentIndex: ${currentIndex}, inputState:`, JSON.parse(JSON.stringify(inputState)));
        if (status !== 'running' || !currentSentenceRef.current || inputInfo.type !== 'release') {
            console.log(`[TanbunChallenge handleInput] Bailing out. status: ${status}, currentSentence: "${currentSentenceRef.current}", inputType: ${inputInfo.type}`);
            return { isExpected: false, shouldGoToNext: false };
        }

        const pressCode = inputInfo.pressCode;
        totalCharsTypedRef.current += 1; // 

        let typedChar: string | null = null;
        let isCorrect = false; // Flag for correctness

        const { char: actualExpectedChar, info: actualExpectedCharInfo } = getActualCurrentTargetDetails(currentSentenceRef.current, currentIndex, layers); // Pass layers
        const expectedChar = actualExpectedChar; // Use this for comparisons and length
        const expectedCharInfo = actualExpectedCharInfo; // Use this for type checks

        const expectedSingleCharPressCode = singleCharKeyCodes[expectedChar];
        console.log(`[TanbunChallenge handleInput] Target char: '${expectedChar}', expectedSingleCharPressCode: ${expectedSingleCharPressCode}, actual pressCode: ${pressCode}`);

        // Special handling for '' (and potentially other multi-key punctuations)
        // This block handles the *first* key press for multi-key punctuation like '' and ''
        if ((expectedChar === '' || expectedChar === '') && inputState.gyouKey !== PUNCTUATION_STEP_1_GYOU_KEY) {
             const layer8 = layers[8];
            const equalKigoPressCode = layer8?.indexOf(EQUAL_KIGO_KEY_NAME); // Use constant
            const periodItselfPressCode = layer8?.indexOf('');
            const commaItselfPressCode = layer8?.indexOf(''); // For ''

            if (inputState.gyouKey === null) { // Expecting the first key "=\n"
                if (equalKigoPressCode !== -1 && pressCode === equalKigoPressCode && inputInfo.layerIndex === 8) {
                    clearInputTimeout();
                    setInputState({ ...initialInputState, gyouKey: PUNCTUATION_STEP_1_GYOU_KEY });
                    inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                    console.log(`[TanbunChallenge handleInput] Punctuation step 1: "${EQUAL_KIGO_KEY_NAME}" pressed. Waiting for punctuation key.`);
                    return { isExpected: true, shouldGoToNext: false };
                } else { // Incorrect first key for '' or ''
                    missCountRef.current += 1; resetInputStateAndClearTimeout(); return { isExpected: false, shouldGoToNext: false };
                }
            } else if (inputState.gyouKey === PUNCTUATION_STEP_1_GYOU_KEY) { // Expecting the second key "" or ""
                if (expectedChar === '' && periodItselfPressCode !== -1 && pressCode === periodItselfPressCode && inputInfo.layerIndex === 8) {
                    typedChar = '';
                    console.log(`[TanbunChallenge handleInput] '' step 2: "" pressed. Typed char: '${typedChar}'.`);
                } else if (expectedChar === '' && commaItselfPressCode !== -1 && pressCode === commaItselfPressCode && inputInfo.layerIndex === 8) {
                    typedChar = '';
                    console.log(`[TanbunChallenge handleInput] '' step 2: "" pressed. Typed char: '${typedChar}'.`);
                } else { // Incorrect second key
                    missCountRef.current += 1;
                    resetInputStateAndClearTimeout();
                    console.log(`[TanbunChallenge handleInput] Punctuation step 2: Incorrect key 0x${pressCode.toString(16)} on layer ${inputInfo.layerIndex}. Expected '${expectedChar}' (code ${expectedChar === '' ? periodItselfPressCode : commaItselfPressCode}) on layer 8.`);
                    return { isExpected: false, shouldGoToNext: false };
                }
            } else if (inputState.gyouKey === PUNCTUATION_STEP_1_GYOU_KEY && !(expectedChar === '' || expectedChar === '')) {
                // If inputState is PUNCTUATION_STEP_1_GYOU_KEY but the target is NOT '' or '', it's an error state.
                missCountRef.current += 1;
                 resetInputStateAndClearTimeout();
                return { isExpected: false, shouldGoToNext: false };
            }
        }

        // 1.  () - This will run if the special '' handling above didn't set typedChar
        // console.log(`[TanbunChallenge handleInput] expectedCharInfo:`, expectedCharInfo);
        
        if (typedChar === null && expectedCharInfo?.type === 'kigo' && expectedSingleCharPressCode !== undefined && expectedSingleCharPressCode !== null && pressCode === expectedSingleCharPressCode) {
            // Ensure layerIndex exists and matches before using it
            if (expectedCharInfo.layerIndex !== undefined && inputInfo.layerIndex === expectedCharInfo.layerIndex) { // Check layerIndex for single chars
                 typedChar = expectedChar; // 
                 console.log(`[TanbunChallenge handleInput] Single char direct match: '${typedChar}'. Resetting inputState.`);
                 setInputState(initialInputState); // 
                 clearInputTimeout();
            } // This 'if' for layerIndex check now correctly closes here.
        } else if (typedChar === null) { // Only proceed to Kana logic if a kigo wasn't typed and typedChar is still null
            // Kana input logic
            const currentGyouKey = hid2Gyou(pressCode + 1, kb, side);
            const currentDanKey = hid2Dan(pressCode + 1, kb, side);
            const funcMap = functionKeyMaps[kb]?.[side] ?? {};
            const funcKeyName = Object.entries(funcMap).find(([idx, _]) => parseInt(idx) === pressCode)?.[1];

            console.log(`[TanbunChallenge handleInput KANA_LOGIC] pressCode: 0x${pressCode.toString(16)}, currentGyouKey: ${currentGyouKey}, currentDanKey: ${currentDanKey}, funcKeyName: ${funcKeyName}, inputState:`, JSON.parse(JSON.stringify(inputState)));

            // Stricter validation for Kana input, similar to KanaChallengePractice
            if (inputState.gyouKey === null) { // Expecting Gyou key or Sokuon or initial Kigo key
                if (funcKeyName === '') {
                    // Only accept Sokuon key if the target character is ''
                    if (expectedCharInfo?.type === 'sokuonKomoji' && expectedCharInfo.isTsu) {
                        typedChar = '';
                        // setInputState(initialInputState); // Reset will happen if typedChar matches expectedChar
                    } else {
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                } else if (currentGyouKey) { // A Gyou key was pressed
                    // Check if the pressed Gyou key matches the Gyou key of the expected character
                    let targetGyouKey: string | undefined | null = null;
                    if (expectedCharInfo) {
                        switch (expectedCharInfo.type) {
                            case 'seion': case 'youon': case 'dakuon': case 'handakuon':
                                targetGyouKey = expectedCharInfo.gyouKey;
                                break;
                            case 'youdakuon': case 'youhandakuon':
                                targetGyouKey = expectedCharInfo.inputDef.gyouKey;
                                break;
                            case 'youonKakucho':
                                targetGyouKey = expectedCharInfo.inputDef?.gyouKey;
                                break;
                            case 'sokuonKomoji': case 'kigo': // These have optional gyouKey
                                targetGyouKey = expectedCharInfo.gyouKey;
                                break;
                        }
                    }
                    if (expectedCharInfo && targetGyouKey === currentGyouKey) {
                        clearInputTimeout();
                        setInputState({ ...initialInputState, gyouKey: currentGyouKey });
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false }; // Correct Gyou key, wait for next
                    } else if (expectedCharInfo && expectedCharInfo.type === 'kigo' && currentGyouKey === '' && expectedCharInfo.keyName === '') {
                        // Handling for kigo sequences that start with pressing the '' gyou key (e.g. for kigoMapping3)
                        clearInputTimeout();
                        setInputState({ ...initialInputState, gyouKey: '' });
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false };
                    }
                     else { // Incorrect Gyou key for the target character
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                } else if (funcKeyName === '') { // Pressed '' function key (potentially for kigoMapping2 or start of kigoMapping3)
                    if (expectedCharInfo && expectedCharInfo.type === 'kigo' && expectedCharInfo.keyName === '') {
                        // This handles cases where '' key itself is the first step of a kigo sequence (e.g. for kigoMapping3)
                        clearInputTimeout();
                        setInputState({ ...initialInputState, gyouKey: '' }); // Set gyouKey to '' to expect kigoMapping3 char next
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false };
                    } else { // Pressed '' but not expected for the current target
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                }
                else { // Not a Gyou key, Sokuon, or Kigo key when one was expected
                    missCountRef.current += 1;
                    resetInputStateAndClearTimeout();
                    return { isExpected: false, shouldGoToNext: false };
                }
            }
            // GyouKey is already set, expecting modifier (Youon, Dakuon, Handakuon) or Dan key or Kigo char
            else if (inputState.gyouKey) {
                if (funcKeyName === '') {
                    let targetGyouKey: string | undefined | null = null;
                    if (expectedCharInfo) {
                        switch (expectedCharInfo.type) {
                            case 'seion': case 'youon': case 'dakuon': case 'handakuon':
                                targetGyouKey = expectedCharInfo.gyouKey;
                                break;
                            case 'youdakuon': case 'youhandakuon':
                                targetGyouKey = expectedCharInfo.inputDef.gyouKey;
                                break;
                            case 'youonKakucho':
                                targetGyouKey = expectedCharInfo.inputDef?.gyouKey;
                                break;
                        }
                    }
                    if (inputState.gyouKey === targetGyouKey &&
                        (expectedCharInfo?.type === 'youon' || expectedCharInfo?.type === 'youdakuon' || expectedCharInfo?.type === 'youhandakuon')) {
                        clearInputTimeout();
                        setInputState(prev => ({ ...prev, hasYouon: true }));
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false };
                    } else { // Youon key pressed inappropriately
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                } else if (funcKeyName === '  ') {
                    let targetGyouKey: string | undefined | null = null;
                    if (expectedCharInfo) {
                        switch (expectedCharInfo.type) {
                            case 'seion': case 'youon': case 'dakuon': case 'handakuon':
                                targetGyouKey = expectedCharInfo.gyouKey;
                                break;
                            case 'youdakuon': case 'youhandakuon':
                                targetGyouKey = expectedCharInfo.inputDef.gyouKey;
                                break;
                            case 'youonKakucho':
                                targetGyouKey = expectedCharInfo.inputDef?.gyouKey;
                                break;
                        }
                    }
                    if (inputState.gyouKey === targetGyouKey &&
                        (expectedCharInfo?.type === 'dakuon' || expectedCharInfo?.type === 'youdakuon' ||
                         (expectedCharInfo?.type === 'handakuon' && inputState.gyouKey === '') || // For  ->  transition
                         (expectedCharInfo?.type === 'youhandakuon' && inputState.gyouKey === ''))) {

                        clearInputTimeout();
                        if (inputState.gyouKey === '' && inputState.hasDakuon && !inputState.hasHandakuon) { // Current state is '', '', etc.
                            // Trying to type '', '', etc.
                            if (expectedCharInfo.type === 'handakuon' || expectedCharInfo.type === 'youhandakuon') {
                                setInputState(prev => ({ ...prev, gyouKey: prev.gyouKey, hasYouon: prev.hasYouon, hasDakuon: false, hasHandakuon: true }));
                            } else { // Incorrect: Dakuon key pressed again when Handakuon was not the target's next step
                                missCountRef.current += 1; resetInputStateAndClearTimeout(); return { isExpected: false, shouldGoToNext: false };
                            }
                        } else if (!inputState.hasDakuon && !inputState.hasHandakuon) { // No dakuon/handakuon yet, set dakuon
                            setInputState(prev => ({ ...prev, gyouKey: prev.gyouKey, hasYouon: prev.hasYouon, hasDakuon: true, hasHandakuon: false }));
                        } else { // Other invalid state for Dakuon key (e.g., already handakuon)
                            missCountRef.current += 1; resetInputStateAndClearTimeout(); return { isExpected: false, shouldGoToNext: false };
                        }
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false };
                    } else { // Dakuon key pressed inappropriately
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                } else if (funcKeyName === '') { // Direct Handakuon key press
                    let targetGyouKey: string | undefined | null = null;
                    if (expectedCharInfo) {
                        switch (expectedCharInfo.type) {
                            // Only Handakuon and Youhandakuon are relevant here for ''
                            case 'handakuon':
                                targetGyouKey = expectedCharInfo.gyouKey;
                                break;
                            case 'youhandakuon':
                                targetGyouKey = expectedCharInfo.inputDef.gyouKey;
                                break;
                        }
                    }
                    if (inputState.gyouKey === '' && inputState.gyouKey === targetGyouKey &&
                        (expectedCharInfo?.type === 'handakuon' || expectedCharInfo?.type === 'youhandakuon')) {
                        clearInputTimeout();
                        setInputState(prev => ({ ...prev, gyouKey: prev.gyouKey, hasYouon: prev.hasYouon, hasDakuon: false, hasHandakuon: true }));
                        inputTimeoutRef.current = window.setTimeout(resetInputStateAndClearTimeout, INPUT_TIMEOUT_MS);
                        return { isExpected: true, shouldGoToNext: false };
                    } else { // Handakuon key pressed inappropriately
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                } else if (funcKeyName === '') { // For kigoMapping2 (e.g.,  +  -> )
                    // This assumes gyouKey is already set (and not '' itself)
                    if (inputState.gyouKey !== '' && kigoMapping2[inputState.gyouKey]) {
                        typedChar = kigoMapping2[inputState.gyouKey];
                        // typedChar will be compared below. If it matches expectedChar, it's correct.
                    } else { // '' key pressed when not applicable for kigoMapping2
                        missCountRef.current += 1;
                        resetInputStateAndClearTimeout();
                        return { isExpected: false, shouldGoToNext: false };
                    }
                }
                // Dan key or Kigo3 character key
                else if (currentDanKey || inputState.gyouKey === '') {
                    clearInputTimeout();
                    const gyou = inputState.gyouKey;
                    if (gyou === '') { // This is for kigoMapping3 (e.g.,  +  ->  )
                        const kigoSymbol = kigoMapping3[pressCode]; // pressCode is the 0-based index of the key on the layer
                        if (kigoSymbol) {
                            typedChar = kigoSymbol.split('\n')[0]; // Take the actual symbol part
                        } else { // Invalid key after '' gyouKey
                            missCountRef.current += 1; resetInputStateAndClearTimeout(); return { isExpected: false, shouldGoToNext: false };
                        }
                    } else if (currentDanKey) { // Standard Kana character composition
                        const danIndexForSeion = danList.indexOf(currentDanKey); // Base index for seion

                        if (inputState.hasYouon && inputState.hasDakuon) { // 
                            const charIndex = youdakuonDanMapping[gyou]?.indexOf(currentDanKey);
                            if (charIndex !== -1 && charIndex !== undefined) {
                                typedChar = youdakuonGyouChars[gyou]?.[charIndex] ?? null;
                            }
                        } else if (inputState.hasYouon && inputState.hasHandakuon) { // 
                            const charIndex = youhandakuonDanMapping[gyou]?.indexOf(currentDanKey);
                            if (charIndex !== -1 && charIndex !== undefined) {
                                typedChar = youhandakuonGyouChars[gyou]?.[charIndex] ?? null;
                            }
                        } else if (inputState.hasYouon) { // 
                            const charIndex = youonDanMapping[gyou]?.indexOf(currentDanKey);
                            if (charIndex !== -1 && charIndex !== undefined) {
                                typedChar = youonGyouChars[gyou]?.[charIndex] ?? null;
                            }
                        } else if (inputState.hasDakuon) { // 
                            const charIndex = dakuonDanMapping[gyou]?.indexOf(currentDanKey);
                            if (charIndex !== -1 && charIndex !== undefined) {
                                 typedChar = dakuonGyouChars[gyou]?.[charIndex] ?? null;
                             }
                        } else if (inputState.hasHandakuon) { // 
                            if (gyou === '') {
                                const charIndex = handakuonDanMapping[gyou]?.indexOf(currentDanKey);
                                if (charIndex !== -1 && charIndex !== undefined) {
                                    typedChar = handakuonGyouChars[gyou]?.[charIndex] ?? null;
                                 }
                            }
                        } else { // 
                            if (gyou === '') { //  has irregular dan mapping for seion
                                if (currentDanKey === '') typedChar = gyouChars[gyou]?.[0] ?? null;
                                else if (currentDanKey === '') typedChar = gyouChars[gyou]?.[1] ?? null;
                                else if (currentDanKey === '') typedChar = gyouChars[gyou]?.[2] ?? null;
                            } else if (danIndexForSeion !== -1) { // Other seion
                                typedChar = gyouChars[gyou]?.[danIndexForSeion] ?? null;
                            }
                        }
                        console.log(`[TanbunChallenge handleInput KANA_COMPOSED] Composed char: '${typedChar}', gyou: ${gyou}, dan: ${currentDanKey}, inputState:`, JSON.parse(JSON.stringify(inputState)));
                        if (typedChar === null) { // Invalid combination for a character
                            missCountRef.current += 1; resetInputStateAndClearTimeout(); return { isExpected: false, shouldGoToNext: false };
                        }
                    }
                } else { // No DanKey, not a funcKey, and gyouKey is set. This is an invalid sequence.
                    missCountRef.current += 1;
                    resetInputStateAndClearTimeout();
                    return { isExpected: false, shouldGoToNext: false };
                }
            }
        }

        if (typedChar !== null) {
            console.log(`[TanbunChallenge handleInput CHAR_COMPARISON] typedChar='${typedChar}', expectedChar='${expectedChar}'`);
            if (typedChar === expectedChar) { // Simple 1-char comparison for now
                isCorrect = true;
                completedCharsCountRef.current += expectedChar.length; // Use length of actual expected char
                const nextIndex = currentIndex + expectedChar.length; // Advance by actual length
                console.log(`[TanbunChallenge handleInput CORRECT] nextIndex: ${nextIndex}, sentenceLength: ${currentSentenceRef.current.length}`);

                if (nextIndex >= currentSentenceRef.current.length) {
                    completedSentenceCountRef.current += 1;
                    console.log(`[TanbunChallenge handleInput CORRECT] Sentence completed. Calling selectNextSentence.`);
                    selectNextSentence();
                    setInputState(initialInputState); // Reset for new sentence
                } else {
                    console.log(`[TanbunChallenge handleInput CORRECT] Advancing to next char. New target: '${currentSentenceRef.current[nextIndex]}'`);
                    setCurrentIndex(nextIndex);
                    setProgressInfo({ typedEndIndex: nextIndex });
                    setCurrentTargetChar(currentSentenceRef.current[nextIndex]);
                    setInputState(initialInputState); // Reset for next char
                    clearInputTimeout();
                }
                return { isExpected: true, shouldGoToNext: false };
            } else {
                missCountRef.current += 1;
                console.log(`[TanbunChallenge handleInput INCORRECT] Typed: '${typedChar}', Expected: '${expectedChar}'. Miss count: ${missCountRef.current}`);
                // On incorrect input, reset inputState to allow fresh input for the current target character
                setInputState(initialInputState);
                clearInputTimeout();
                return { isExpected: false, shouldGoToNext: false };
            }
        }
        // If typedChar is still null, it means it was a state-changing key press (like Gyou key) or an invalid sequence start
        console.log(`[TanbunChallenge handleInput] typedChar is null, returning based on previous logic (likely state change or invalid start).`);
        return { isExpected: false, shouldGoToNext: false }; // Default if no char was typed

    }, [status, currentSentenceRef, currentIndex, selectNextSentence, kb, side, funcKeyCodes, inputState,
        clearInputTimeout, resetInputStateAndClearTimeout, singleCharKeyCodes, setCurrentTargetChar, layers]
    );

    useEffect(() => { console.log(`[TanbunChallenge useEffect currentIndex] currentIndex updated to: ${currentIndex}, currentTargetChar is now: '${currentTargetChar}'`); }, [currentIndex, currentTargetChar]);

    const getHighlightClassName = useCallback((keyName: string, layoutIndex: number, keyIndex: number): PracticeHighlightResult => {
        const noHighlight: PracticeHighlightResult = { className: null, overrideKey: null };
        if (status !== 'running' || !currentSentenceRef.current || currentIndex >= currentSentenceRef.current.length) {
            // console.log(`[TanbunChallenge getHighlightClassName] Bailing: status=${status}, no sentence or index out of bounds.`);
            return noHighlight;
        }

        const { char: actualTargetCharString, info: targetKanaInfo } = getActualCurrentTargetDetails(currentSentenceRef.current, currentIndex, layers);
        const expectedSingleCharPressCode = singleCharKeyCodes[actualTargetCharString];
        // console.log(`[TanbunChallenge getHighlightClassName] Actual Target: '${actualTargetCharString}', inputState:`, JSON.parse(JSON.stringify(inputState)), `keyName: '${keyName}', layoutIndex: ${layoutIndex}`);

        // Special highlighting for '' and '' targets
        if (actualTargetCharString === '' || actualTargetCharString === '') {
            const punctuationKeyName = actualTargetCharString; // '' or ''
            if (inputState.gyouKey === PUNCTUATION_STEP_1_GYOU_KEY) {
                // Expecting the punctuation key itself on layer 8
                if (layoutIndex === 8 && keyName.trim() === punctuationKeyName) {
                    return { className: 'bg-blue-100', overrideKey: null };
                }
            } else {
                // Expecting "=\n" key on layer 8
                if (layoutIndex === 8 && keyName.trim() === EQUAL_KIGO_KEY_NAME) {
                    return { className: 'bg-blue-100', overrideKey: null };
                }
            }
            // If target is punctuation but the current key is not the expected one for the current step, don't highlight other keys.
            return noHighlight;
        }

        // 1. 
        // Check if the current key is the expected single character key
        if (targetKanaInfo?.type === 'kigo' && expectedSingleCharPressCode !== undefined && expectedSingleCharPressCode !== null) {
            const targetKeyNameInLayout = actualTargetCharString === '' ? EQUAL_KIGO_KEY_NAME : actualTargetCharString;
            if (targetKanaInfo.layerIndex !== undefined && layoutIndex === targetKanaInfo.layerIndex && keyIndex === expectedSingleCharPressCode) {
                // console.log(`[TanbunChallenge getHighlightClassName] Highlighting single char: '${targetKeyNameInLayout}' on layer ${layoutIndex}`);
                return { className: 'bg-blue-100', overrideKey: null };
            }
        }
        // 2. 
        // targetKanaInfo is already derived from getActualCurrentTargetDetails
        if (!targetKanaInfo) {
            // console.log(`[TanbunChallenge getHighlightClassName] No KanaTargetInfo for '${actualTargetCharString}'.`);
            return noHighlight;
        }

        let expectedKeyName: string | null = null;
        let targetLayoutIdx: number | null = null;

        if (inputState.gyouKey === null) { // 1 ( or  or  or single char) 
            if (targetKanaInfo.type === 'sokuonKomoji' && targetKanaInfo.isTsu) {
                expectedKeyName = '';
            } else if (targetKanaInfo.type === 'youdakuon' || targetKanaInfo.type === 'youhandakuon') {
                expectedKeyName = targetKanaInfo.inputDef.gyouKey;
            } else if (targetKanaInfo.type === 'kigo' && 'gyouKey' in targetKanaInfo && targetKanaInfo.gyouKey) {
                expectedKeyName = targetKanaInfo.gyouKey;
            } else if (targetKanaInfo.type === 'seion' || targetKanaInfo.type === 'youon' || targetKanaInfo.type === 'dakuon' || targetKanaInfo.type === 'handakuon') {
                expectedKeyName = targetKanaInfo.gyouKey;
            } else if (targetKanaInfo.type === 'kigo' && targetKanaInfo.keyName === '') { // For kigoMapping3 start
                 expectedKeyName = '';
            }

            targetLayoutIdx = 2;
        } else if (inputState.gyouKey === '') { // kigoPractice3 2
            //  (8)
            expectedKeyName = actualTargetCharString; // 
            targetLayoutIdx = 8;
        } else if (
            (targetKanaInfo.type === 'youon' && !inputState.hasYouon) ||
            (targetKanaInfo.type === 'youdakuon' && !inputState.hasYouon) ||
            (targetKanaInfo.type === 'youhandakuon' && !inputState.hasYouon)
        ) {
            expectedKeyName = '';
            targetLayoutIdx = 2;
        } else if ( // 
            (targetKanaInfo.type === 'dakuon' && !inputState.hasDakuon && !inputState.hasHandakuon) || // 
            (targetKanaInfo.type === 'youdakuon' && inputState.hasYouon && !inputState.hasDakuon && !inputState.hasHandakuon) // 
        ) {
            expectedKeyName = '  ';
            targetLayoutIdx = 2;
        } else if (
            (targetKanaInfo.type === 'handakuon' && inputState.gyouKey === '' && !inputState.hasHandakuon) || //  ()
            (targetKanaInfo.type === 'youhandakuon' && inputState.gyouKey === '' && inputState.hasYouon && !inputState.hasHandakuon) //  ()
        ) {
            expectedKeyName = '  ';
            targetLayoutIdx = 2;
        } else { //  () 
            targetLayoutIdx = 3;
            if (targetKanaInfo.type === 'sokuonKomoji' && targetKanaInfo.isTsu) {
                expectedKeyName = null;
            } else if (inputState.gyouKey) {
                if (
                    targetKanaInfo.type === 'seion' ||
                    (targetKanaInfo.type === 'youon' && inputState.hasYouon) ||
                    (targetKanaInfo.type === 'dakuon' && inputState.hasDakuon) ||
                    (targetKanaInfo.type === 'handakuon' && inputState.hasHandakuon) ||
                    (targetKanaInfo.type === 'youdakuon' && inputState.hasYouon && inputState.hasDakuon) ||
                    (targetKanaInfo.type === 'youhandakuon' && inputState.hasYouon && inputState.hasHandakuon)
                ) {
                    if (targetKanaInfo.type === 'youdakuon' || targetKanaInfo.type === 'youhandakuon') {
                        expectedKeyName = targetKanaInfo.inputDef.dan;
                    } else if (targetKanaInfo.type === 'seion' || targetKanaInfo.type === 'youon' || targetKanaInfo.type === 'dakuon' || targetKanaInfo.type === 'handakuon') {
                        expectedKeyName = targetKanaInfo.danKey;
                    }
                }
            }
        }

        // console.log(`[TanbunChallenge getHighlightClassName] Expected key: '${expectedKeyName}', Target layout: ${targetLayoutIdx}`);
        if (expectedKeyName && layoutIndex === targetLayoutIdx && keyName === expectedKeyName) {
            // console.log(`[TanbunChallenge getHighlightClassName] Highlighting: '${keyName}' on layer ${layoutIndex}`);
            return { className: 'bg-blue-100', overrideKey: null };
        }

        return noHighlight;
    }, [status, currentSentenceRef, currentIndex, inputState, singleCharKeyCodes, layers, kb, side, funcKeyCodes]);
    // Add layers to dependencies

    // Derived current Kana target details for App.tsx and potentially other uses
    const currentKanaTargetDetails = useMemo(() => {
        console.log(`[TanbunChallenge useMemo currentKanaTargetDetails] status: ${status}, currentSentenceRef.current: "${currentSentenceRef.current}", currentIndex: ${currentIndex}`);
        // Use the new helper function to get the full target details
        const { info } = getActualCurrentTargetDetails(currentSentenceRef.current, currentIndex, layers); // Pass layers
        if (status === 'running') {
            console.log(`[TanbunChallenge useMemo currentKanaTargetDetails] Details:`, info);
            return info;
        }
        console.log(`[TanbunChallenge useMemo currentKanaTargetDetails] Status not running, returning null.`);
        return null; // Return null if not running
    }, [status, currentSentenceRef, currentIndex, layers]); // currentSentenceRef is a ref, so its .current value change won't trigger re-memoization directly. This is fine if currentSentence state is used for triggering.
    // Add layers to dependencies
    const practiceResult = useMemo(() => {
        let calculatedHeadingChars: string[] = [];
        const charToDisplay = status === 'finished' ? null : (currentTargetChar ?? undefined); // Use state currentTargetChar
        console.log(`[TanbunChallenge useMemo practiceResult] status: ${status}, countdownValue: ${countdownValue}, currentSentence: "${currentSentence}", currentTargetChar (state): "${currentTargetChar}", charToDisplay: "${charToDisplay}"`);

        if (status === 'countdown') {
            calculatedHeadingChars = [`${countdownValue}`];
        } else if (status === 'finished') {
            calculatedHeadingChars = [''];
        } else if (status === 'running' && currentSentence) { // Use state currentSentence
            calculatedHeadingChars = [currentSentence];
        }

        let customTargetLayerIndices = showKeyLabels ? [2, 3] : [];
        if (charToDisplay === '' || charToDisplay === '') { // 8
            customTargetLayerIndices = [8];
        }


        return {
            headingChars: calculatedHeadingChars,
            handleInput,
            getProgressInfo: () => progressInfo,
            reset,
            challengeResults,
            targetLayerIndex: null,
            displayLayers: undefined,
            getHighlightClassName,
            isInvalidInputTarget: () => false,
            typedEndIndex: progressInfo.typedEndIndex,
            status,
            targetChar: charToDisplay,
            currentTarget: currentKanaTargetDetails ?? undefined,
            countdownValue,
            challengeTimer: remainingTime,
            challengeTotalTime: TRAINING_DURATION_MS,
            targetLayerIndices: customTargetLayerIndices,
        };
    }, [
        status, countdownValue, currentSentence, // Use state currentSentence
        currentTargetChar, // Use state currentTargetChar
        handleInput, currentKanaTargetDetails,
        reset,
        challengeResults,
        progressInfo,
        remainingTime,
        showKeyLabels,
        getHighlightClassName, layers // Add layers to dependencies of useMemo if getActualCurrentTargetDetails uses it inside a callback that's part of this memo
    ]);

    return practiceResult;
};

export default useTanbunChallengePractice;
